"use client"

import React, { useState, useEffect } from 'react'
import { Banner } from '../components/ui/banner'
import { X } from 'lucide-react'
import { Button } from './ui/button'

interface TimeLeft {
  days: number
  hours: number
  minutes: number
  seconds: number
  expired: boolean
}

export default function CountdownBanner() {
  const [timeLeft, setTimeLeft] = useState<TimeLeft>({ days: 0, hours: 0, minutes: 0, seconds: 0, expired: false })
  const [mounted, setMounted] = useState(false)
  const [isVisible, setIsVisible] = useState(true)

  useEffect(() => {
    setMounted(true)
  }, [])

  useEffect(() => {
    // Target: November 21, 2025 at 2:00 PM IST (which is 08:30 UTC)
    const targetDate = new Date('2025-11-21T08:30:00.000Z')

    const calculateTimeLeft = () => {
      const now = new Date()
      const difference = targetDate.getTime() - now.getTime()

      if (difference <= 0) {
        return { days: 0, hours: 0, minutes: 0, seconds: 0, expired: true }
      }

      return {
        days: Math.floor(difference / (1000 * 60 * 60 * 24)),
        hours: Math.floor((difference / (1000 * 60 * 60)) % 24),
        minutes: Math.floor((difference / 1000 / 60) % 60),
        seconds: Math.floor((difference / 1000) % 60),
        expired: false
      }
    }

    // Initial calculation
    setTimeLeft(calculateTimeLeft())

    // Update every second
    const timer = setInterval(() => {
      setTimeLeft(calculateTimeLeft())
    }, 1000)

    return () => clearInterval(timer)
  }, [])

  if (!mounted || !isVisible || timeLeft.expired) {
    return null
  }

  const handleClose = () => {
    setIsVisible(false)
  }

  return (
    <div 
      id="countdown-banner-container"
      className="w-full transition-all duration-300 ease-in-out"
      style={{
        background: 'linear-gradient(90deg, rgb(0, 124, 202) 0%, rgb(0, 100, 160) 100%)',
      }}
    >
      {/* Desktop view - two lines */}
      <div className="hidden md:block">
        <div className="flex items-center justify-between px-6 py-4 max-w-7xl mx-auto">
          <div className="flex flex-col gap-1 flex-1">
            {/* First line with countdown */}
            <div className="flex items-center gap-4">
              <span className="text-base font-semibold text-white whitespace-nowrap">
                Early bird registration opens in:
              </span>
              
              {/* Countdown */}
              <div className="flex items-center gap-2 text-white font-bold tabular-nums text-lg">
                <span>{String(timeLeft.days).padStart(2, '0')}d</span>
                <span className="text-white/50">:</span>
                <span>{String(timeLeft.hours).padStart(2, '0')}h</span>
                <span className="text-white/50">:</span>
                <span>{String(timeLeft.minutes).padStart(2, '0')}m</span>
                <span className="text-white/50">:</span>
                <span>{String(timeLeft.seconds).padStart(2, '0')}s</span>
              </div>
            </div>
            
            {/* Second line */}
            <span className="text-sm text-white/90">
              Early registration unlocks limited-edition Do Drones merchandise and curated bonus perks.
            </span>
          </div>

          {/* Close button */}
          <Button
            variant="ghost"
            size="sm"
            className="h-8 w-8 p-0 hover:bg-white/10 text-white/80 hover:text-white ml-4"
            onClick={handleClose}
          >
            <X className="h-5 w-5" />
          </Button>
        </div>
      </div>

      {/* Mobile view - compact two lines */}
      <div className="block md:hidden">
        <div className="flex items-start justify-between px-4 py-3 gap-2">
          <div className="flex flex-col gap-1.5 flex-1">
            {/* First line with countdown */}
            <div className="flex items-center gap-2 flex-wrap">
              <span className="text-xs font-medium text-white/90">
                Early bird registration opens in:
              </span>
              
              {/* Compact countdown */}
              <div className="flex items-center gap-1 text-white font-semibold tabular-nums text-xs">
                <span>{String(timeLeft.days).padStart(2, '0')}d</span>
                <span className="text-white/50">:</span>
                <span>{String(timeLeft.hours).padStart(2, '0')}h</span>
                <span className="text-white/50">:</span>
                <span>{String(timeLeft.minutes).padStart(2, '0')}m</span>
                <span className="text-white/50">:</span>
                <span>{String(timeLeft.seconds).padStart(2, '0')}s</span>
              </div>
            </div>
            
            {/* Second line */}
            <span className="text-xs text-white/85 leading-relaxed">
              Early registration unlocks limited-edition Do Drones merchandise and curated bonus perks.
            </span>
          </div>

          {/* Close button */}
          <Button
            variant="ghost"
            size="sm"
            className="h-6 w-6 p-0 hover:bg-white/10 text-white/80 hover:text-white flex-shrink-0"
            onClick={handleClose}
          >
            <X className="h-4 w-4" />
          </Button>
        </div>
      </div>
    </div>
  )
}
