'use client'

import { useEffect, useState } from 'react'

export default function FlowBackdrop() {
  const [prefersReducedMotion, setPrefersReducedMotion] = useState(false)
  
  useEffect(() => {
    const mediaQuery = window.matchMedia('(prefers-reduced-motion: reduce)')
    setPrefersReducedMotion(mediaQuery.matches)
    
    const handleChange = (e: MediaQueryListEvent) => {
      setPrefersReducedMotion(e.matches)
    }
    
    mediaQuery.addEventListener('change', handleChange)
    return () => mediaQuery.removeEventListener('change', handleChange)
  }, [])
  
  return (
    <div 
      className="absolute inset-0 overflow-hidden pointer-events-none"
      aria-hidden="true"
    >
      {/* Top radial gradient vignette */}
      <div
        className="absolute inset-0"
        style={{
          background: 'radial-gradient(60% 50% at 50% 10%, rgba(0,124,202,.20), transparent 60%)',
        }}
      />
      
      {/* Left blurred oval */}
      <div
        className="absolute top-1/4 -left-32 w-96 h-96 rounded-full"
        style={{
          background: 'rgba(255,255,255,.06)',
          filter: 'blur(60px)',
          opacity: prefersReducedMotion ? 1 : undefined,
          animation: prefersReducedMotion ? 'none' : 'pulse-subtle 8s ease-in-out infinite',
        }}
      />
      
      {/* Right blurred oval */}
      <div
        className="absolute bottom-1/4 -right-32 w-96 h-96 rounded-full"
        style={{
          background: 'rgba(255,255,255,.06)',
          filter: 'blur(60px)',
          opacity: prefersReducedMotion ? 1 : undefined,
          animation: prefersReducedMotion ? 'none' : 'pulse-subtle 10s ease-in-out infinite 2s',
        }}
      />
      
      {/* SVG noise texture */}
      <svg
        className="absolute inset-0 w-full h-full"
        style={{ opacity: 0.03 }}
      >
        <filter id="noise">
          <feTurbulence
            type="fractalNoise"
            baseFrequency="0.8"
            numOctaves="4"
            stitchTiles="stitch"
          />
          <feColorMatrix type="saturate" values="0" />
        </filter>
        <rect width="100%" height="100%" filter="url(#noise)" />
      </svg>
    </div>
  )
}
