'use client'

import { useEffect, useRef } from 'react'
import { useNavigate } from 'react-router-dom'
import Header from '../components/Header'
import BrandTitle from '../components/BrandTitle'
import FlowBackdrop from '../components/FlowBackdrop'
import { InteractiveHoverButton } from '../components/ui/interactive-hover-button'
import Footer from '../components/Footer'
import BesleyWord from '../components/BesleyWord'
import { siteConfig } from '../lib/siteConfig'
import { DISABLE_LOGIN_PAGE } from '../flags'

export default function ProgramPage() {
  const navigate = useNavigate()
  const sectionsRef = useRef<(HTMLElement | null)[]>([])

  useEffect(() => {
    const observerOptions = {
      threshold: 0.1,
      rootMargin: '0px 0px -10% 0px'
    }

    const observer = new IntersectionObserver((entries) => {
      entries.forEach((entry) => {
        if (entry.isIntersecting) {
          entry.target.classList.add('visible')
        }
      })
    }, observerOptions)

    sectionsRef.current.forEach((section) => {
      if (section) observer.observe(section)
    })

    return () => observer.disconnect()
  }, [])

  const addToRefs = (el: HTMLElement | null) => {
    if (el && !sectionsRef.current.includes(el)) {
      sectionsRef.current.push(el)
    }
  }

  const handleRegisterClick = () => {
    navigate('/login')
  }

  const handleMapsClick = () => {
    window.open('https://maps.app.goo.gl/3kE947tA9R8UQpX2A?g_st=aw', '_blank', 'noopener,noreferrer')
  }

  // 5-Day Schedule Data
  const scheduleData = [
    {
      day: 'Day 1',
      date: 'Dec 09, 2025 (Tuesday)',
      title: 'Foundation & Introduction',
      sessions: [
        { time: '09:0009:45', activity: 'Inauguration', duration: '45 min' },
        { time: '10:0010:45', activity: 'Introduction to UAV Systems', duration: '45 min' },
        { time: '11:0011:45', activity: 'Aerodynamics & Fixed Wing Design - I', duration: '45 min' },
        { time: '12:0012:45', activity: 'Avionics - I', duration: '45 min' },
        { time: '12:4514:00', activity: 'Lunch', duration: '75 min' },
        { time: '14:0014:45', activity: 'Autopilot & Control Systems - I', duration: '45 min' },
        { time: '15:0015:45', activity: 'Structures & Analysis - I', duration: '45 min' },
        { time: '16:0016:45', activity: 'Propulsion Systems - I', duration: ' 45 min' },
        { time: '17:0017:30', activity: 'High Tea & Networking', duration: '30 min' }
      ]
    },
    {
      day: 'Day 2',
      date: 'Dec 10, 2025 (Wednesday)',
      title: 'Avionics, UAV Design & Practicals',
      sessions: [
        { time: '09:0009:45', activity: 'Aerodynamics & Fixed Wing Design - II', duration: '45 min' },
        { time: '10:0010:45', activity: 'Avionics - II', duration: '45 min' },
        { time: '11:0011:45', activity: 'Drone Building Methodology (Multirotor)', duration: '45 min' },
        { time: '12:0012:45', activity: 'Guest Lecture 1', duration: '45 min' },
        { time: '12:4514:00', activity: 'Lunch', duration: '75 min' },
        { time: '14:0017:00', activity: 'Practical 1', duration: '180 min' },
        { time: '17:0017:30', activity: 'High Tea & Networking', duration: '30 min' }
      ]
    },
    {
      day: 'Day 3',
      date: 'Dec 11, 2025 (Thursday)',
      title: 'Control, Analysis & Practicals',
      sessions: [
        { time: '09:0009:45', activity: 'Autopilot & Control Systems - II', duration: '45 min' },
        { time: '10:0010:45', activity: 'Structures & Analysis - II', duration: '45 min' },
        { time: '11:0011:45', activity: 'Propulsion Systems - II', duration: '45 min' },
        { time: '12:0012:45', activity: 'Guest Lecture 2', duration: '45 min' },
        { time: '12:4514:00', activity: 'Lunch', duration: '75 min' },
        { time: '14:0017:00', activity: 'Practical 2', duration: '180 min' },
        { time: '17:0017:30', activity: 'High Tea & Networking', duration: '30 min' }
      ]
    },
    {
      day: 'Day 4',
      date: 'Dec 12, 2025 (Friday)',
      title: 'Ground Control, Software & Practicals',
      sessions: [
        { time: '09:0009:45', activity: 'Ground Control & Mission Planning', duration: '45 min' },
        { time: '10:0010:45', activity: 'Drone Programming & ROS', duration: '45 min' },
        { time: '11:0011:45', activity: 'AI & Open CV on Drones', duration: '45 min' },
        { time: '12:0012:45', activity: 'Guest Lecture 3', duration: '45 min' },
        { time: '12:4514:00', activity: 'Lunch', duration: '75 min' },
        { time: '14:0017:00', activity: 'Practical 3', duration: '180 min' },
        { time: '17:0017:30', activity: 'High Tea & Networking', duration: '30 min' }
      ]
    },
    {
      day: 'Day 5',
      date: 'Dec 13, 2025 (Saturday)',
      title: 'Flight Operations & Certification',
      sessions: [
        { time: '09:0012:45', activity: 'Flight Operations & Demonstration', duration: '225 min' },
        { time: '12:4514:00', activity: 'Lunch', duration: '75 min' },
        { time: '14:0014:45', activity: 'Guest Lecture 4', duration: '45 min' },
        { time: '15:0015:45', activity: 'Special Session', duration: '45 min' },
        { time: '16:0016:45', activity: 'Valedictory', duration: ' 45 min' },
        { time: '17:0017:30', activity: 'High Tea & Networking', duration: '30 min' }
      ]
    }
  ]

  return (
    <div className="relative min-h-screen" style={{ background: 'var(--brand-black)' }}>
      {/* <Header /> - HIDDEN: Using TubelightNavBar instead */}
      <FlowBackdrop />
      
      {/* PHASE 1: Hero Section - 5-day workshop */}
      <section className="relative z-10 pt-32 pb-20 px-4">
        <div className="container mx-auto max-w-6xl">
          <div className="text-center mb-8">
            <div className="flex justify-center mb-10">
              <div className="text-4xl md:text-5xl lg:text-6xl xl:text-7xl">
                <BrandTitle size="lg" variant="dark" className="!text-4xl !md:text-5xl !lg:text-6xl !xl:text-7xl" />
              </div>
            </div>
            <h1
              className="text-3xl md:text-5xl lg:text-6xl xl:text-7xl font-outfit font-light mb-6 leading-tight"
              style={{ color: 'var(--brand-white)' }}
            >
              Your <BesleyWord>journey</BesleyWord> to mastering aerial robotics
            </h1>
            <p 
              className="text-xl md:text-2xl font-manrope max-w-3xl mx-auto mb-8"
              style={{ color: 'rgba(255,255,255,0.7)' }}
            >
              A comprehensive five-day workshop that takes you from first principles to confident multirotor and fixed-wing flight, taught by the IISc UAV Lab team at India's premier aerospace research institute.
            </p>
          </div>
        </div>
      </section>

      {/* PHASE 2: What's Included Section */}
      <section 
        ref={addToRefs}
        className="reveal-section relative z-10 py-20 px-4"
      >
        <div className="container mx-auto max-w-6xl">
          <h2
            className="text-3xl md:text-5xl font-outfit font-light mb-12 text-center"
            style={{ color: 'var(--brand-white)' }}
          >
            what's <BesleyWord>included</BesleyWord>?
          </h2>
          
          <div className="grid md:grid-cols-3 gap-8">
            {[
              {
                title: 'Hands-on Training Kits & Hardware',
                items: [
                  'Use complete multirotor and fixed-wing training kits during guided sessions.',
                  'Access flight controllers, sensors, radios, and ground-control systems used in real UAV projects.',
                  'Work with calibrated tools and lab infrastructure from the IISc UAV Laboratory.',
                  'Practice safe assembly, calibration, and troubleshooting under mentor supervision.'
                ]
              },
              {
                title: 'Expert-Led Teaching & Mentorship',
                items: [
                  'Learn directly from IISc faculty, researchers, and experienced UAV practitioners.',
                  'Experience a structured blend of theory, demonstrations, and hands-on labs across five days.',
                  'Get step-by-step guidance on avionics setup, mission planning, and flight testing.',
                  'Interact in small groups for Q&A, debugging help, and personalised feedback.'
                ]
              },
              {
                title: 'Labs, Flights & Learning Resources',
                items: [
                  'Perform indoor tests, outdoor demonstrations, and simulator-based training as per the schedule.',
                  'Observe full mission workflows from pre-flight checks to post-flight debriefs.',
                  'Receive curated course material and workshop notes for continued self-study.',
                  'Earn a co-endorsed certificate of completion from IISc UAV Lab, HAL SDC, and MeitY.'
                ]
              }
            ].map((item, idx) => (
              <div
                key={idx}
                className="stagger-item"
                style={{
                  background: 'rgba(255,255,255,0.05)',
                  backdropFilter: 'blur(10px)',
                  border: '1px solid rgba(255,255,255,0.1)',
                  borderRadius: '16px',
                  padding: '32px',
                  animationDelay: `${idx * 0.1}s`
                }}
              >
                <h3
                  className="text-2xl font-manrope font-bold mb-4"
                  style={{ color: 'var(--brand-accent)' }}
                >
                  {item.title}
                </h3>
                <ul className="space-y-3">
                  {item.items.map((subitem, subidx) => (
                    <li
                      key={subidx}
                      className="flex items-start gap-3"
                      style={{ color: 'rgba(255,255,255,0.8)' }}
                    >
                      <span style={{ color: 'var(--brand-accent)' }}></span>
                      <span className="font-manrope text-sm">{subitem}</span>
                    </li>
                  ))}
                </ul>
              </div>
            ))}
          </div>
          
          {/* Kit Note */}
          <p 
            className="text-sm text-center mt-8"
            style={{ color: 'rgba(255,255,255,0.4)' }}
          >
            Note: Training kits are issued for on-campus use during the workshop and remain the property of IISc after the event.
          </p>
        </div>
      </section>

      {/* PHASE 5: Key Takeaways */}
      <section 
        ref={addToRefs}
        className="reveal-section relative z-10 py-20 px-4"
      >
        <div className="container mx-auto max-w-5xl">
          <h2
            className="text-3xl md:text-5xl font-outfit font-light mb-12 text-center"
            style={{ color: 'var(--brand-white)' }}
          >
            key <BesleyWord>takeaways</BesleyWord>.
          </h2>
          
          <div className="grid md:grid-cols-2 gap-6">
            {[
              'Build and work with complete multirotor and fixed-wing UAV training platforms from structure to avionics.',
              'Develop a solid grasp of aerodynamics, propulsion, avionics, and guidancenavigationcontrol for small UAVs.',
              'Gain practical experience in assembly, calibration, and pre-flight checks under expert supervision.',
              'Learn to plan, configure, and monitor autonomous missions using professional ground-control software.',
              'Understand key DGCA regulations, safety protocols, and responsible UAS operations in the Indian context.',
              'Observe live indoor and outdoor flight demonstrations that connect classroom concepts to real missions.',
              'Interact with IISc researchers, industry experts, and fellow participants to explore projects and career paths.',
              'Receive a co-endorsed certificate of completion recognising your participation in Do Drones 2025.'
            ].map((takeaway, idx) => (
              <div
                key={idx}
                className="flex items-start gap-4 p-6 rounded-xl stagger-item"
                style={{
                  background: 'rgba(255,255,255,0.03)',
                  border: '1px solid rgba(255,255,255,0.1)',
                  animationDelay: `${idx * 0.05}s`
                }}
              >
                <div
                  className="w-8 h-8 rounded-full flex items-center justify-center shrink-0 mt-1"
                  style={{ 
                    background: 'var(--brand-accent)',
                    color: 'var(--brand-white)'
                  }}
                >
                  <span className="text-sm font-bold"></span>
                </div>
                <span 
                  className="text-lg font-manrope"
                  style={{ color: 'rgba(255,255,255,0.9)' }}
                >
                  {takeaway}
                </span>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* PHASE 3: Workshop Schedule - 5 Days, Tentative */}
      <section 
        ref={addToRefs}
        className="reveal-section relative z-10 py-20 px-4"
      >
        <div className="container mx-auto max-w-5xl">
          <h2
            className="text-3xl md:text-5xl font-outfit font-light mb-6 text-center"
            style={{ color: 'var(--brand-white)' }}
          >
            workshop <BesleyWord>details</BesleyWord>.
          </h2>
          
          <p 
            className="text-center text-sm mb-12 font-manrope"
            style={{ color: 'rgba(255,255,255,0.6)' }}
          >
            This is a tentative schedule and may be updated closer to the workshop date.
          </p>

          {scheduleData.map((day, idx) => (
            <div
              key={idx}
              className="mb-12 stagger-item"
              style={{
                background: 'rgba(0,124,202,0.05)',
                backdropFilter: 'blur(10px)',
                border: '1px solid rgba(0,124,202,0.2)',
                borderRadius: '20px',
                padding: '40px',
                animationDelay: `${idx * 0.15}s`
              }}
            >
              <div className="mb-8">
                <div className="flex items-baseline gap-4 mb-2">
                  <h3
                    className="text-4xl md:text-5xl font-outfit font-bold"
                    style={{ color: 'var(--brand-accent)' }}
                  >
                    {day.day}
                  </h3>
                  <span 
                    className="text-lg font-manrope"
                    style={{ color: 'rgba(255,255,255,0.6)' }}
                  >
                    {day.date}
                  </span>
                </div>
                <h4
                  className="text-2xl md:text-3xl font-outfit font-light"
                  style={{ color: 'var(--brand-white)' }}
                >
                  {day.title}
                </h4>
              </div>

              <div className="space-y-3">
                {day.sessions.map((slot, slotIdx) => (
                  <div
                    key={slotIdx}
                    className="flex items-center gap-4 p-3 rounded-lg"
                    style={{ background: 'rgba(255,255,255,0.03)' }}
                  >
                    <span 
                      className="text-sm font-mono font-semibold w-32 shrink-0"
                      style={{ color: 'var(--brand-accent)' }}
                    >
                      {slot.time}
                    </span>
                    <span 
                      className="flex-1 font-manrope"
                      style={{ color: 'rgba(255,255,255,0.85)' }}
                    >
                      {slot.activity}
                    </span>
                    {slot.duration && (
                      <span 
                        className="text-sm font-manrope shrink-0"
                        style={{ color: 'rgba(255,255,255,0.5)' }}
                      >
                        {slot.duration}
                      </span>
                    )}
                  </div>
                ))}
              </div>
            </div>
          ))}
        </div>
      </section>

      {/* PHASE 4: Venue & Facilities (Merged from Venue page) */}
      <section 
        ref={addToRefs}
        className="reveal-section relative z-10 py-20 px-4"
      >
        <div className="container mx-auto max-w-6xl">
          <h2
            className="text-3xl md:text-5xl font-outfit font-light mb-12 text-center"
            style={{ color: 'var(--brand-white)' }}
          >
            Venue
          </h2>

          {/* Venue Card */}
          <div 
            className="rounded-3xl overflow-hidden mb-20"
            style={{
              background: 'rgba(0,124,202,0.05)',
              backdropFilter: 'blur(20px)',
              border: '2px solid rgba(0,124,202,0.2)'
            }}
          >
            <div className="p-8 md:p-12">
              <div className="mb-8">
                <div 
                  className="inline-block px-6 py-2 rounded-full mb-6"
                  style={{
                    background: 'rgba(0,124,202,0.2)',
                    border: '1px solid rgba(0,124,202,0.4)'
                  }}
                >
                  <span 
                    className="text-sm font-manrope font-bold uppercase tracking-wider"
                    style={{ color: 'var(--brand-accent)' }}
                  >
                    Official Venue
                  </span>
                </div>
                
                <h3
                  className="text-3xl md:text-5xl font-outfit font-light mb-4"
                  style={{ color: 'var(--brand-white)' }}
                >
                  AE Auditorium
                </h3>
                <h4
                  className="text-2xl md:text-3xl font-manrope font-bold mb-4"
                  style={{ color: 'var(--brand-accent)' }}
                >
                  Department of Aerospace Engineering
                </h4>
                <p 
                  className="text-xl font-manrope mb-2"
                  style={{ color: 'rgba(255,255,255,0.8)' }}
                >
                  Indian Institute of Science (IISc)
                </p>
                <p 
                  className="text-lg font-manrope mb-6"
                  style={{ color: 'rgba(255,255,255,0.6)' }}
                >
                  Mathikere, Bengaluru  560012, Karnataka, India
                </p>
              </div>

              <div 
                className="p-6 rounded-2xl mb-6"
                style={{
                  background: 'rgba(255,255,255,0.03)',
                  border: '1px solid rgba(255,255,255,0.1)'
                }}
              >
                <p 
                  className="text-lg font-manrope leading-relaxed"
                  style={{ color: 'rgba(255,255,255,0.85)' }}
                >
                  Core theory sessions and key talks are held at the AE Auditorium, Department of Aerospace Engineering, IISc. Participants also visit UAV Laboratory spaces and designated test areas on campus for demonstrations, labs, and flight activities as per the workshop schedule.
                </p>
              </div>

              <InteractiveHoverButton
                text="Open in Google Maps"
                variant="primary"
                onClick={handleMapsClick}
              />
            </div>
          </div>

          {/* World-class Facilities Grid */}
          <h3
            className="text-3xl md:text-5xl font-outfit font-light mb-12 text-center"
            style={{ color: 'var(--brand-white)' }}
          >
            world-class <BesleyWord>facilities</BesleyWord>.
          </h3>
          
          <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-6 mb-20">
            {[
              {
                title: 'Research Laboratory',
                description: 'Advanced aerospace engineering lab with tools and rigs for UAV assembly, integration, and testing.'
              },
              {
                title: 'Indoor Flying Arena',
                description: 'Controlled indoor space for initial hover tests, manoeuvring practice, and sensor checks.'
              },
              {
                title: 'Outdoor Test Range',
                description: 'Designated outdoor area for live multirotor and fixed-wing flight demonstrations (weather and permissions permitting).'
              },
              {
                title: 'Flight Simulation Lab',
                description: 'Digital flight simulators for risk-free training and mission rehearsal before outdoor flights.'
              },
              {
                title: 'AE Auditorium',
                description: 'Modern auditorium for lectures, presentations, and demonstrations with full AV facilities.'
              },
              {
                title: 'Safety-Certified Areas',
                description: 'Practice zones meeting safety norms, with procedures for emergency response and first aid.'
              }
            ].map((facility, idx) => (
              <div
                key={idx}
                className="stagger-item p-8 rounded-2xl"
                style={{
                  background: 'rgba(255,255,255,0.03)',
                  backdropFilter: 'blur(10px)',
                  border: '1px solid rgba(255,255,255,0.1)',
                  animationDelay: `${idx * 0.1}s`
                }}
              >
                <h4
                  className="text-xl font-manrope font-bold mb-3"
                  style={{ color: 'var(--brand-white)' }}
                >
                  {facility.title}
                </h4>
                <p 
                  className="font-manrope leading-relaxed text-sm"
                  style={{ color: 'rgba(255,255,255,0.7)' }}
                >
                  {facility.description}
                </p>
              </div>
            ))}
          </div>

          {/* Getting There */}
          <h3
            className="text-3xl md:text-5xl font-outfit font-light mb-12 text-center"
            style={{ color: 'var(--brand-white)' }}
          >
            getting <BesleyWord>there</BesleyWord>.
          </h3>
          
          <div className="grid md:grid-cols-2 gap-8">
            <div 
              className="p-8 rounded-2xl"
              style={{
                background: 'rgba(255,255,255,0.03)',
                border: '1px solid rgba(255,255,255,0.1)'
              }}
            >
              <h4
                className="text-xl font-manrope font-bold mb-4"
                style={{ color: 'var(--brand-white)' }}
              >
                Campus Access
              </h4>
              <p 
                className="font-manrope leading-relaxed"
                style={{ color: 'rgba(255,255,255,0.7)' }}
              >
                Confirmed participants will receive detailed entry instructions, gate information, and campus access guidelines by email before the workshop.
              </p>
            </div>

            <div 
              className="p-8 rounded-2xl"
              style={{
                background: 'rgba(255,255,255,0.03)',
                border: '1px solid rgba(255,255,255,0.1)'
              }}
            >
              <h4
                className="text-xl font-manrope font-bold mb-4"
                style={{ color: 'var(--brand-white)' }}
              >
                Local Transport
              </h4>
              <p 
                className="font-manrope leading-relaxed"
                style={{ color: 'rgba(255,255,255,0.7)' }}
              >
                IISc Bengaluru is well connected by metro, bus, and taxi services. The nearest metro stations are Yeshwanthpur and Sandal Soap Factory. Limited parking is available for private vehicles on campus.
              </p>
            </div>
          </div>
        </div>
      </section>

      {/* PHASE 6: Final CTA Section */}
      <section 
        ref={addToRefs}
        className="reveal-section relative z-10 py-20 px-4 mb-20"
      >
        <div 
          className="container mx-auto max-w-4xl text-center p-12 rounded-3xl"
          style={{
            background: 'rgba(0,124,202,0.1)',
            backdropFilter: 'blur(20px)',
            border: '2px solid rgba(0,124,202,0.3)'
          }}
        >
          <h3
            className="text-3xl md:text-5xl font-outfit font-light mb-6"
            style={{ color: 'var(--brand-white)' }}
          >
            ready to <BesleyWord>join us</BesleyWord>?
          </h3>
          <p 
            className="text-xl font-manrope mb-8"
            style={{ color: 'rgba(255,255,255,0.7)' }}
          >
            Secure your seat at Do Drones 2025  a five-day intensive UAV workshop at IISc Bengaluru.
          </p>
          <div className="flex justify-center">
            <InteractiveHoverButton
              text="Register Now"
              variant="primary"
              onClick={handleRegisterClick}
              className={(DISABLE_LOGIN_PAGE?" disabled-link":"")}
            />
          </div>
        </div>
      </section>

      <Footer />

      <style jsx>{`
        .reveal-section {
          opacity: 0;
          transform: translateY(50px);
          transition: opacity 1s ease, transform 1s ease;
        }

        .reveal-section.visible {
          opacity: 1;
          transform: translateY(0);
        }

        .stagger-item {
          opacity: 0;
          transform: translateY(30px);
          animation: none;
        }

        .reveal-section.visible .stagger-item {
          animation: fadeInUp 0.8s ease forwards;
        }

        @keyframes fadeInUp {
          to {
            opacity: 1;
            transform: translateY(0);
          }
        }

        @media (prefers-reduced-motion: reduce) {
          .reveal-section,
          .stagger-item {
            transition: none;
            animation: none;
            opacity: 1;
            transform: none;
          }
        }
      `}</style>
    </div>
  )
}
