/**
 * SponsorsMarquee Component
 * 
 * Cycles through sponsor roles (diamond  organisers  partners  gold) every 3-4 seconds.
 * Each role displays with a heading and animated logo carousel.
 * 
 * FEATURES:
 * - Role-based slides that rotate automatically
 * - 3-4 second transitions between roles
 * - LogoCarousel with blur animations for each role
 * - Larger, more prominent logos with refined typography
 * - Config-driven from lib/siteConfig.ts
 * 
 * HOW TO ADD SPONSORS:
 * - Update lib/siteConfig.ts sponsors arrays (organisers, diamond, partners, gold)
 * - Each sponsor needs: { name, logo, url, role }
 * - Component automatically picks up changes
 */

"use client";

import React, { useEffect, useState, useRef } from "react";
import { AnimatePresence, motion } from "framer-motion";
import { siteConfig } from "../lib/siteConfig";
import { LogoCarousel, type CarouselLogo } from "./LogoCarousel";
import type { SponsorRole } from "../lib/types";

export default function SponsorsMarquee() {
  // Define role order
  const allRoles: SponsorRole[] = ['diamond', 'organiser', 'partner', 'gold'];
  
  // Filter out roles with no sponsors
  const availableRoles = allRoles.filter((role) => {
    const sponsors = siteConfig.sponsors[`${role === 'organiser' ? 'organisers' : role === 'partner' ? 'partners' : role}` as keyof typeof siteConfig.sponsors];
    return Array.isArray(sponsors) && sponsors.length > 0;
  });
  
  const [roleIndex, setRoleIndex] = useState(0);
  const [isPaused, setIsPaused] = useState(false); // Pause rotation on hover
  const intervalRef = useRef<NodeJS.Timeout | null>(null);

  // Rotate to next role every 3-4 seconds (pauses on hover)
  useEffect(() => {
    if (availableRoles.length <= 1) return; // No need to cycle if only one role
    
    if (intervalRef.current) {
      clearInterval(intervalRef.current);
    }
    
    // Only advance role when not paused
    intervalRef.current = setInterval(() => {
      if (!isPaused) {
        setRoleIndex((prev) => (prev + 1) % availableRoles.length);
      }
    }, 3500); // 3.5 seconds per role
    
    return () => {
      if (intervalRef.current) {
        clearInterval(intervalRef.current);
      }
    };
  }, [availableRoles.length, isPaused]);

  if (availableRoles.length === 0) {
    return null; // No sponsors to show
  }

  const currentRole = availableRoles[roleIndex];
  
  // Get sponsors for current role
  const getSponsorsForRole = (role: SponsorRole) => {
    const key = role === 'organiser' ? 'organisers' : role === 'partner' ? 'partners' : role;
    return siteConfig.sponsors[key as keyof typeof siteConfig.sponsors] || [];
  };
  
  const currentSponsors = getSponsorsForRole(currentRole);
  
  // Transform to CarouselLogo format
  const currentRoleLogos: CarouselLogo[] = currentSponsors.map((sponsor, index) => ({
    id: `${currentRole}-${index}`,
    name: sponsor.name,
    logo: sponsor.logo,
    url: sponsor.url,
  }));
  
  // Get heading text for current role
  const getRoleHeading = (role: SponsorRole): string => {
    const sponsorCount = currentSponsors.length;
    switch (role) {
      case 'diamond':
        return sponsorCount > 1 ? 'Our Diamond Sponsors' : 'Our Diamond Sponsor';
      case 'organiser':
        return 'Our Organisers';
      case 'partner':
        return 'Our Partners';
      case 'gold':
        return 'Our Gold Sponsors';
      default:
        return 'Our Sponsors';
    }
  };
  
  // Get color for current role
  const getRoleColor = (role: SponsorRole): string => {
    switch (role) {
      case 'diamond':
        return 'var(--blue-300)';
      case 'partner':
        return 'var(--brand-accent)';
      case 'gold':
        return '#FFC107';
      case 'organiser':
      default:
        return 'var(--blue-100)';
    }
  };

  return (
    <div className="w-full">
      <div 
        className="relative mx-auto max-w-5xl rounded-3xl border border-white/5 bg-gradient-to-b from-white/5 to-transparent px-6 py-8 sm:px-8 sm:py-10 md:px-10 md:py-12 shadow-[0_18px_60px_rgba(0,0,0,0.6)]"
        onMouseEnter={() => setIsPaused(true)}
        onMouseLeave={() => setIsPaused(false)}
      >
        {/* Headings - refined typography with animation */}
        <div className="text-center mb-6 sm:mb-8">
          {/* Small line - PRESENTED BY */}
          <p
            className="text-xs sm:text-sm uppercase tracking-widest font-medium mb-2"
            style={{
              color: 'var(--blue-100)',
              opacity: 0.7,
              letterSpacing: '0.15em'
            }}
          >
            Presented by
          </p>
          
          {/* Role-specific heading with AnimatePresence for smooth transitions */}
          <AnimatePresence mode="wait">
            <motion.h2
              key={currentRole}
              className="font-besley italic font-semibold text-base sm:text-lg md:text-xl tracking-tight"
              style={{
                color: getRoleColor(currentRole),
              }}
              initial={{ y: 10, opacity: 0, filter: "blur(4px)" }}
              animate={{
                y: 0,
                opacity: 1,
                filter: "blur(0px)",
                transition: {
                  type: "spring",
                  stiffness: 300,
                  damping: 20,
                  mass: 1,
                  duration: 0.4,
                },
              }}
              exit={{
                y: -10,
                opacity: 0,
                filter: "blur(4px)",
                transition: {
                  type: "tween",
                  ease: "easeIn",
                  duration: 0.2,
                },
              }}
            >
              {getRoleHeading(currentRole)}
            </motion.h2>
          </AnimatePresence>
        </div>
        
        {/* Logo carousel for current role - increased prominence */}
        <div className="mt-6 sm:mt-8">
          <LogoCarousel
            columnCount={Math.min(3, currentRoleLogos.length)}
            logos={currentRoleLogos}
            cycleIntervalMs={3000}
          />
        </div>
      </div>
    </div>
  );
}
