/**
 * LogoCarousel Component
 * 
 * Animated logo carousel with blur-in/blur-out transitions using Framer Motion.
 * Distributes logos across N columns that cycle independently.
 * 
 * FEATURES:
 * - 3-second transitions per logo (configurable via cycleIntervalMs)
 * - Pause on hover/focus for better UX
 * - Clickable logos that open sponsor URLs in new tab
 * - Spring-based animations with blur effects
 * - Larger, more prominent logo sizes
 * 
 * HOW TO USE:
 * - Pass an array of CarouselLogo objects with id, name, logo path, and url
 * - Set columnCount (default 3) for number of logo columns
 * - Adjust cycleIntervalMs (default 3000ms) to change transition speed
 */

"use client";

import React, {
  useCallback,
  useEffect,
  useMemo,
  useState,
} from "react";
import { AnimatePresence, motion } from "framer-motion";

export interface CarouselLogo {
  id: string;
  name: string;
  logo: string; // Image path from siteConfig (e.g., "/sponsors/artpark.svg")
  url: string;  // Sponsor URL
}

interface LogoColumnProps {
  logos: CarouselLogo[];
  index: number;
  currentTime: number;
  cycleIntervalMs: number;
  paused: boolean;
}

interface LogoCarouselProps {
  columnCount?: number;
  logos: CarouselLogo[];
  cycleIntervalMs?: number; // Default 3000ms (3 seconds per logo)
}

// Helper: Shuffle array randomly
const shuffleArray = <T,>(array: T[]): T[] => {
  const shuffled = [...array];
  for (let i = shuffled.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1));
    [shuffled[i], shuffled[j]] = [shuffled[j], shuffled[i]];
  }
  return shuffled;
};

// Helper: Distribute logos evenly across columns
const distributeLogos = (allLogos: CarouselLogo[], columnCount: number): CarouselLogo[][] => {
  if (allLogos.length === 0) return [];
  
  const shuffled = shuffleArray(allLogos);
  const columns: CarouselLogo[][] = Array.from({ length: columnCount }, () => []);

  // Distribute logos round-robin
  shuffled.forEach((logo, index) => {
    columns[index % columnCount].push(logo);
  });

  // Fill columns to same length with duplicates if needed
  const maxLength = Math.max(...columns.map((col) => col.length));
  columns.forEach((col) => {
    while (col.length < maxLength) {
      col.push(shuffled[Math.floor(Math.random() * shuffled.length)]);
    }
  });

  return columns;
};

/**
 * LogoColumn
 * 
 * Individual column that cycles through its assigned logos.
 * Uses Framer Motion for smooth blur-in/blur-out transitions.
 */
const LogoColumn: React.FC<LogoColumnProps> = React.memo(
  ({ logos, index, currentTime, cycleIntervalMs, paused }) => {
    if (!logos.length) return null;

    // Calculate which logo to show based on time and cycle interval
    const columnDelay = index * 200; // Stagger columns by 200ms
    const adjustedTime = paused 
      ? currentTime % (cycleIntervalMs * logos.length)
      : (currentTime + columnDelay) % (cycleIntervalMs * logos.length);
    const currentIndex = Math.floor(adjustedTime / cycleIntervalMs);
    const currentLogo = useMemo(
      () => logos[currentIndex],
      [logos, currentIndex]
    );

    return (
      <motion.div
        className="relative h-24 w-40 overflow-hidden sm:h-28 sm:w-48 md:h-32 md:w-60 lg:h-36 lg:w-64"
        initial={{ opacity: 0, y: 50 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{
          delay: index * 0.1,
          duration: 0.5,
          ease: "easeOut",
        }}
      >
        <AnimatePresence mode="wait">
          <motion.div
            key={`${currentLogo.id}-${currentIndex}`}
            className="absolute inset-0 flex items-center justify-center"
            initial={{ y: "10%", opacity: 0, filter: "blur(8px)" }}
            animate={{
              y: "0%",
              opacity: 1,
              filter: "blur(0px)",
              transition: {
                type: "spring",
                stiffness: 300,
                damping: 20,
                mass: 1,
                bounce: 0.2,
                duration: 0.5,
              },
            }}
            exit={{
              y: "-20%",
              opacity: 0,
              filter: "blur(6px)",
              transition: {
                type: "tween",
                ease: "easeIn",
                duration: 0.3,
              },
            }}
          >
            {/* Clickable logo link */}
            <a
              href={currentLogo.url}
              target="_blank"
              rel="noopener noreferrer"
              className="inline-flex items-center justify-center h-full w-full p-2"
              aria-label={`Visit ${currentLogo.name} website`}
              title={currentLogo.name}
            >
              <img
                src={currentLogo.logo}
                alt={currentLogo.name}
                className={`object-contain opacity-90 hover:opacity-100 transition-opacity ${
                  currentLogo.name === 'SwaYaan' 
                    ? 'max-h-[130%] max-w-[130%]' 
                    : 'max-h-[90%] max-w-[90%]'
                }`}
              />
            </a>
          </motion.div>
        </AnimatePresence>
      </motion.div>
    );
  }
);

LogoColumn.displayName = "LogoColumn";

/**
 * LogoCarousel
 * 
 * Main carousel component that manages timing and column distribution.
 * Automatically pauses when user hovers/focuses any logo.
 */
export function LogoCarousel({ 
  columnCount = 3, 
  logos, 
  cycleIntervalMs = 3000 // 3 seconds per logo (fast transitions)
}: LogoCarouselProps) {
  const [logoSets, setLogoSets] = useState<CarouselLogo[][]>([]);
  const [currentTime, setCurrentTime] = useState(0);
  const [paused, setPaused] = useState(false); // Pause state for hover/focus

  // Increment time by 100ms, but only when not paused
  const updateTime = useCallback(() => {
    setCurrentTime((prevTime) => prevTime + 100);
  }, []);

  // Timer that respects pause state
  useEffect(() => {
    if (paused) return; // Don't update time when paused (hover/focus)
    
    const intervalId = setInterval(updateTime, 100);
    return () => clearInterval(intervalId);
  }, [updateTime, paused]);

  // Distribute logos across columns on mount or when logos change
  useEffect(() => {
    const distributedLogos = distributeLogos(logos, columnCount);
    setLogoSets(distributedLogos);
  }, [logos, columnCount]);

  if (!logos.length) return null;

  return (
    <div 
      className="flex items-center justify-center gap-6 sm:gap-8 md:gap-10 lg:gap-12"
      onMouseEnter={() => setPaused(true)}
      onMouseLeave={() => setPaused(false)}
      onFocus={() => setPaused(true)}
      onBlur={() => setPaused(false)}
    >
      {logoSets.map((colLogos, index) => (
        <LogoColumn
          key={index}
          logos={colLogos}
          index={index}
          currentTime={currentTime}
          cycleIntervalMs={cycleIntervalMs}
          paused={paused}
        />
      ))}
    </div>
  );
}
