/**
 * BesleyWord Component
 * 
 * Replaces the handwriting/cursive blue accent words with Besley Italic semibold.
 * Used for words like: journey, included, details, takeaways, innovation, facilities, etc.
 */

import React from 'react'

interface BesleyWordProps {
  children: React.ReactNode
}

export default function BesleyWord({ children }: BesleyWordProps) {
  return (
    <span 
      className="font-besley italic font-semibold inline-block"
      style={{
        color: 'var(--brand-primary)', // Brand blue
        letterSpacing: '-0.01em',
      }}
    >
      {children}
    </span>
  )
}
