import React from 'react'

interface BrandTitleProps {
  className?: string
  size?: 'sm' | 'md' | 'lg'
  variant?: 'light' | 'dark'
}

export default function BrandTitle({ className = '', size = 'lg', variant = 'dark' }: BrandTitleProps) {
  const sizes = {
    sm: 'text-xl md:text-2xl',
    md: 'text-2xl md:text-3xl',
    lg: 'text-3xl md:text-4xl lg:text-5xl'
  }
  
  // Colors based on brochure design
  const colors = {
    dark: {
      do: '#E5E5E5',        // Light gray for dark backgrounds
      drones: '#A0A0A0',    // Medium gray
      year: '#007CCA',      // Bright blue
      period: '#A0A0A0'     // Same as drones
    },
    light: {
      do: '#2A2A2A',        // Dark gray for light backgrounds
      drones: '#9E9E9E',    // Light gray
      year: '#007CCA',      // Bright blue
      period: '#9E9E9E'     // Same as drones
    }
  }
  
  const colorScheme = colors[variant]
  
  return (
    <h1 
      className={`font-manrope font-bold lowercase tracking-tight ${sizes[size]} ${className}`}
      style={{
        letterSpacing: '-0.01em',
        lineHeight: '1.1'
      }}
    >
      <span style={{ color: colorScheme.do }}>do</span>
      {' '}
      <span style={{ color: colorScheme.drones }}>drones</span>
      {' '}
      <span style={{ color: colorScheme.year }}>2025</span>
      <span style={{ color: colorScheme.period }}>.</span>
    </h1>
  )
}
