'use client'

import { useEffect } from 'react'
import { useNavigate } from 'react-router-dom'
import AccentWord from './AccentWord'
import FlowBackdrop from './FlowBackdrop'
import { InteractiveHoverButton } from '../components/ui/interactive-hover-button'
import { siteConfig } from '../lib/siteConfig'
import { DISABLE_LOGIN_PAGE } from '../flags'
export default function HomeHero() {
  const navigate=useNavigate();
  
  useEffect(() => {
    console.log('Hero styled: symphony-mode')
  }, [])
  
  const handleRegisterClick = () => {
    navigate('/login');
  };
  
  const handleProgramClick = () => {
    navigate('/program');
  };
  
  return (
    <section 
      className="relative flex items-center justify-center overflow-hidden px-4"
      style={{ background: 'var(--brand-black)' }}
    >
      {/* Background Flow */}
      <FlowBackdrop />
      
      {/* Hero Content - MD3-style spacing */}
      <div className="relative z-10 w-full max-w-6xl mx-auto text-center py-12 md:py-16 lg:py-20">
        {/* Event Name - Main Branding - Matching Logo Style */}
        <div className="mb-6 md:mb-8">
          <h1 
            className="text-5xl sm:text-6xl md:text-7xl lg:text-8xl xl:text-[9rem] font-manrope font-extrabold leading-[0.95] tracking-tight lowercase"
          >
            <span style={{ color: '#F7F7F7' }}>do </span>
            <span style={{ color: '#696969' }}>drones </span>
            <span style={{ color: '#007CCA' }}>2025</span>
            <span style={{ color: '#F7F7F7' }}>.</span>
          </h1>
        </div>
        
        {/* Date Badge */}
        <div className="inline-block mb-6 md:mb-8 px-5 py-2.5 rounded-full border-2 border-[var(--brand-primary)]/30 bg-[var(--brand-primary)]/10 backdrop-blur-sm">
          <p className="text-sm md:text-base font-semibold" style={{ color: 'var(--brand-primary)' }}>
            {siteConfig.eventDatesFull}
          </p>
        </div>
        
        {/* Tagline */}
        <h2 
          className="text-3xl sm:text-4xl md:text-5xl lg:text-6xl font-manrope font-bold leading-tight mb-4 md:mb-5"
          style={{ color: 'var(--brand-white)' }}
        >
          Learn. Build. <AccentWord>Fly</AccentWord>.
        </h2>
        
        {/* Subheadline - consistent spacing */}
        <p 
          className="text-lg sm:text-xl md:text-2xl mb-3 md:mb-4 max-w-3xl mx-auto font-medium px-2"
          style={{ color: 'var(--brand-white)', opacity: 0.9 }}
        >
          Five transformative days of end-to-end drone mastery
        </p>
        
        <p 
          className="text-sm sm:text-base md:text-lg mb-8 md:mb-10 max-w-2xl mx-auto px-2"
          style={{ opacity: 0.7, color: 'var(--brand-white)' }}
        >
          From core theory to confident flight, mentored by IISc UAV Lab experts.
          Build, tune, and fly both multirotor and fixed-wing platforms.
        </p>
        
        {/* Seats Left Counter - Controlled by siteConfig.showSeatsCounter */}
        {siteConfig.showSeatsCounter && (
          <div className="mb-8 md:mb-10">
            {siteConfig.registrationsOpen ? (
              siteConfig.seatsLeft > 0 ? (
                <p 
                  className="text-sm md:text-base font-medium"
                  style={{ color: 'var(--brand-accent)' }}
                >
                  Only {siteConfig.seatsLeft} seats left
                </p>
              ) : (
                <p 
                  className="text-sm md:text-base font-medium"
                  style={{ color: 'var(--blue-300)', opacity: 0.8 }}
                >
                  Limited seats  registrations subject to availability
                </p>
              )
            ) : (
              <p 
                className="text-sm md:text-base font-medium"
                style={{ color: 'var(--blue-300)', opacity: 0.8 }}
              >
                Registrations are currently closed.
              </p>
            )}
          </div>
        )}
        
        {/* Button Row - Extra generous spacing */}
        <div className="flex flex-col sm:flex-row items-stretch sm:items-center justify-center gap-5 md:gap-8 max-w-md sm:max-w-none mx-auto px-2">
          {/* Primary CTA - Register Now (Blue) - CAPITALIZED */}
          <InteractiveHoverButton
            text="Register Now"
            variant="primary"
            onClick={handleRegisterClick}
            className={"w-full sm:w-auto"+(DISABLE_LOGIN_PAGE?" disabled-link":"")}
          />
          
          {/* Secondary CTA - View Program (Light) - CAPITALIZED */}
          <InteractiveHoverButton
            text="View Program"
            variant="secondary"
            onClick={handleProgramClick}
            className="w-full sm:w-auto"
          />
        </div>
      </div>
    </section>
  )
}
