'use client'

export default function BuildBadge() {
  const buildTag = process.env.NEXT_PUBLIC_BUILD_TAG ?? 'dev'
  const isDev = process.env.NODE_ENV === 'development'
  const shouldShow = isDev || process.env.NEXT_PUBLIC_BUILD_TAG
  
  if (!shouldShow) return null
  
  return (
    <div 
      className="fixed bottom-4 right-4 px-3 py-1.5 bg-black/40 backdrop-blur-sm rounded text-xs font-mono text-white pointer-events-none z-50"
      style={{ opacity: 0.6 }}
    >
      build: {buildTag}
    </div>
  )
}
