"use client"

import React, { useEffect, useState } from "react"
import { motion } from "framer-motion"
import { Link, useLocation } from "react-router-dom"
import { Home, Calendar, Info, HelpCircle } from "lucide-react"
import { cn } from "../../lib/utils"
import { DISABLE_ABOUT_PAGE, DISABLE_LOGIN_PAGE } from "../../flags"

interface NavItem {
  name: string
  url: string
  icon: React.ComponentType<{ size?: number; strokeWidth?: number }>
}

interface NavBarProps {
  items: NavItem[]
  className?: string
}

export function TubelightNavBar({ items, className }: NavBarProps) {
  const pathname = useLocation().pathname
  const [activeTab, setActiveTab] = useState("")
  const [isMobile, setIsMobile] = useState(false)
  const [bannerHeight, setBannerHeight] = useState(0)

  useEffect(() => {
    // Set active tab based on current pathname
    const currentItem = items.find(item => {
      if (item.url === "/" && pathname === "/") return true
      if (item.url !== "/" && pathname.startsWith(item.url)) return true
      return false
    })
    if (currentItem) {
      setActiveTab(currentItem.name)
    }
  }, [pathname, items])

  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth < 768)
    }

    handleResize()
    window.addEventListener("resize", handleResize)
    return () => window.removeEventListener("resize", handleResize)
  }, [])

  useEffect(() => {
    // Observe banner height changes
    const updateBannerHeight = () => {
      const banner = document.getElementById('countdown-banner-container')
      if (banner) {
        setBannerHeight(banner.offsetHeight)
      } else {
        setBannerHeight(0)
      }
    }

    // Initial update
    updateBannerHeight()

    // Create observer for banner changes
    const observer = new MutationObserver(updateBannerHeight)
    const targetNode = document.body

    if (targetNode) {
      observer.observe(targetNode, {
        childList: true,
        subtree: true,
        attributes: true,
        attributeFilter: ['style', 'class']
      })
    }

    // Also listen to window resize
    window.addEventListener('resize', updateBannerHeight)

    // Check periodically in case of dynamic changes
    const interval = setInterval(updateBannerHeight, 500)

    return () => {
      observer.disconnect()
      window.removeEventListener('resize', updateBannerHeight)
      clearInterval(interval)
    }
  }, [])

  const handleLoginClick = () => {
    window.location.href = '/login'
  }

  const handleWhatsAppClick = () => {
    window.open('https://whatsapp.com/channel/0029VbB9lAQ6buMQE4Rj0O00', '_blank', 'noopener,noreferrer')
  }

  return (
    <div
      className={cn(
        "fixed left-1/2 -translate-x-1/2 z-40 pt-6 transition-all duration-300 ease-in-out",
        className,
      )}
      style={{ top: `${bannerHeight}px` }}
    >
      <div
        className="flex items-center gap-3 border backdrop-blur-lg py-1 px-1 rounded-full shadow-lg"
        style={{
          background: 'rgba(7, 7, 7, 0.85)',
          borderColor: 'rgba(255, 255, 255, 0.1)'
        }}
      >
        {items.map((item) => {
          const Icon = item.icon
          const isActive = activeTab === item.name

          return (
            <Link
              key={item.name}
              to={item.url}
              onClick={() => setActiveTab(item.name)}
              className={cn(
                "relative cursor-pointer text-sm font-semibold px-6 py-2 rounded-full transition-colors",
                "hover:text-[var(--brand-primary)]",
                isActive ? "text-[var(--brand-primary)]" : "text-white/70",
                (DISABLE_ABOUT_PAGE && item.url == '/about') ? "disabled-link" : "",
              )}
            >
              <span className="hidden md:inline">{item.name}</span>
              <span className="md:hidden">
                <Icon size={18} strokeWidth={2.5} />
              </span>
              {isActive && (
                <motion.div
                  layoutId="lamp"
                  className="absolute inset-0 w-full rounded-full -z-10"
                  style={{ background: 'rgba(0, 124, 202, 0.1)' }}
                  initial={false}
                  transition={{
                    type: "spring",
                    stiffness: 300,
                    damping: 30,
                  }}
                >
                  <div
                    className="absolute -top-2 left-1/2 -translate-x-1/2 w-8 h-1 rounded-t-full"
                    style={{ background: 'var(--brand-primary)' }}
                  >
                    <div
                      className="absolute w-12 h-6 rounded-full blur-md -top-2 -left-2"
                      style={{ background: 'rgba(0, 124, 202, 0.3)' }}
                    />
                    <div
                      className="absolute w-8 h-6 rounded-full blur-md -top-1"
                      style={{ background: 'rgba(0, 124, 202, 0.3)' }}
                    />
                    <div
                      className="absolute w-4 h-4 rounded-full blur-sm top-0 left-2"
                      style={{ background: 'rgba(0, 124, 202, 0.3)' }}
                    />
                  </div>
                </motion.div>
              )}
            </Link>
          )
        })}

        {/* Divider */}
        <div className="h-6 w-px bg-white/10 mx-1" />

        {/* Login Button */}
        <button
          onClick={handleLoginClick}
          className={"text-sm font-semibold px-4 py-2 rounded-full transition-colors text-white/70 hover:text-white hover:bg-white/5" + (DISABLE_LOGIN_PAGE ? " disabled-link" : "")}
        >
          <span className="hidden md:inline">Login</span>
          <span className="md:hidden">
            <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M11 16l-4-4m0 0l4-4m-4 4h14m-5 4v1a3 3 0 01-3 3H6a3 3 0 01-3-3V7a3 3 0 013-3h7a3 3 0 013 3v1" />
            </svg>
          </span>
        </button>

        {/* Join WhatsApp Button */}
        <button
          onClick={handleWhatsAppClick}
          className="text-sm font-semibold px-4 py-2 rounded-full transition-all flex items-center gap-2"
          style={{
            background: '#25D366',
            color: 'white'
          }}
        >
          <svg className="w-4 h-4" fill="currentColor" viewBox="0 0 24 24">
            <path d="M17.472 14.382c-.297-.149-1.758-.867-2.03-.967-.273-.099-.471-.148-.67.15-.197.297-.767.966-.94 1.164-.173.199-.347.223-.644.075-.297-.15-1.255-.463-2.39-1.475-.883-.788-1.48-1.761-1.653-2.059-.173-.297-.018-.458.13-.606.134-.133.298-.347.446-.52.149-.174.198-.298.298-.497.099-.198.05-.371-.025-.52-.075-.149-.669-1.612-.916-2.207-.242-.579-.487-.5-.669-.51-.173-.008-.371-.01-.57-.01-.198 0-.52.074-.792.372-.272.297-1.04 1.016-1.04 2.479 0 1.462 1.065 2.875 1.213 3.074.149.198 2.096 3.2 5.077 4.487.709.306 1.262.489 1.694.625.712.227 1.36.195 1.871.118.571-.085 1.758-.719 2.006-1.413.248-.694.248-1.289.173-1.413-.074-.124-.272-.198-.57-.347m-5.421 7.403h-.004a9.87 9.87 0 01-5.031-1.378l-.361-.214-3.741.982.998-3.648-.235-.374a9.86 9.86 0 01-1.51-5.26c.001-5.45 4.436-9.884 9.888-9.884 2.64 0 5.122 1.03 6.988 2.898a9.825 9.825 0 012.893 6.994c-.003 5.45-4.437 9.884-9.885 9.884m8.413-18.297A11.815 11.815 0 0012.05 0C5.495 0 .16 5.335.157 11.892c0 2.096.547 4.142 1.588 5.945L.057 24l6.305-1.654a11.882 11.882 0 005.683 1.448h.005c6.554 0 11.89-5.335 11.893-11.893a11.821 11.821 0 00-3.48-8.413Z" />
          </svg>
          <span className="hidden md:inline">Join</span>
        </button>
      </div>
    </div>
  )
}

// Default nav items for Do Drones
export const defaultNavItems: NavItem[] = [
  {
    name: "Home",
    url: "/",
    icon: Home,
  },
  {
    name: "Program",
    url: "/program",
    icon: Calendar,
  },
  {
    name: "About",
    url: "/about",
    icon: Info,
  },
  {
    name: "FAQ",
    url: "/faq",
    icon: HelpCircle,
  },
]
