import React from 'react'

interface AccentWordProps {
  children: React.ReactNode
  as?: 'span' | 'em'
  color?: 'primary' | 'white'
}

export default function AccentWord({ 
  children, 
  as: Component = 'span',
  color = 'primary'
}: AccentWordProps) {
  return (
    <Component 
      className="font-lovelace inline-block"
      style={{
        color: color === 'primary' ? 'var(--brand-primary)' : 'var(--brand-white)',
        fontWeight: 600,
        letterSpacing: 0,
        fontSize: '1.02em',
        lineHeight: 0.9,
        transform: 'translateY(0.02em)',
      }}
    >
      {children}
    </Component>
  )
}
