'use client'

import { useEffect, useRef, useState } from 'react'
import { useNavigate } from 'react-router-dom';
import Header from '../components/Header'
import BrandTitle from '../components/BrandTitle'
import FlowBackdrop from '../components/FlowBackdrop'
import { InteractiveHoverButton } from '../components/ui/interactive-hover-button'
import Footer from '../components/Footer'
import BesleyWord from '../components/BesleyWord'
import { siteConfig } from '../lib/siteConfig'
import { DISABLE_LOGIN_PAGE } from '../flags';

export default function FAQPage() {
  const navigate = useNavigate();
  const sectionsRef = useRef<(HTMLElement | null)[]>([])
  const [openIndex, setOpenIndex] = useState<number | null>(null)

  useEffect(() => {
    const observerOptions = {
      threshold: 0.1,
      rootMargin: '0px 0px -10% 0px'
    }

    const observer = new IntersectionObserver((entries) => {
      entries.forEach((entry) => {
        if (entry.isIntersecting) {
          entry.target.classList.add('visible')
        }
      })
    }, observerOptions)

    sectionsRef.current.forEach((section) => {
      if (section) observer.observe(section)
    })

    return () => observer.disconnect()
  }, [])

  const addToRefs = (el: HTMLElement | null) => {
    if (el && !sectionsRef.current.includes(el)) {
      sectionsRef.current.push(el)
    }
  }

  const handleRegisterClick = () => {
    navigate('/login')
  }

  const handleWhatsAppClick = () => {
    window.open(siteConfig.urls.whatsappChannel, '_blank', 'noopener,noreferrer')
  }

  const faqs = [
    // Eligibility
    {
      category: 'Eligibility',
      q: 'Who can participate in Do Drones 2025?',
      a: 'The workshop is open to students, drone enthusiasts, hobbyists, and professionals interested in aerial robotics. No prior experience with drones or programming is required. Whether you\'re a complete beginner or have some background, our instructors will guide you through every step.'
    },
    
    // Registration
    {
      category: 'Registration',
      q: 'How many participants will be selected?',
      a: 'Only 75 participants will be selected on a first-come, first-served basis. Given the limited seats and expected demand, we strongly recommend registering early to secure your spot.'
    },
    {
      category: 'Registration',
      q: 'When does registration open?',
      a: 'Registration opens on 22 November 2025 (Saturday). You\'ll be able to register through our portal and complete payment via the DIGITS platform.'
    },
    {
      category: 'Registration',
      q: 'What is the last date to register?',
      a: 'The registration deadline is 5 December 2025, or earlier if all 75 seats are filled. Once seats are full, registrations will be closed, so we encourage you to complete your registration as soon as possible after it opens.'
    },
    {
      category: 'Registration',
      q: 'What benefits do Early Bird registrants receive?',
      a: 'The Early Bird window offers a unique advantage: the first 40 participants who register before 28th December 2025 will receive various complimentary, limited-edition Do Drones merchandise along with curated bonus perks revealed exclusively through our official channels.\nThis privilege is strictly limited to the Early Bird period and will not be extended.\nFor those seeking the most elevated Do Drones experience, early registration secures exclusive items and recognition available only to our earliest learners.'
    },
    
    // Fees
    {
      category: 'Fees',
      q: 'What is the registration fee?',
      a: 'Student - 6,000 \n Faculty - 9,000 \n Working Professionals - 12,000 \n The fee covers participation in all five days of the workshop, on-campus use of training kits and tools, course materials, and eligibility for certification (subject to attendance requirements).\n Note: the fees mentioned above are not inclusive of taxes'
    },
    {
      category: 'Fees',
      q: 'Is the registration fee refundable?',
      a: 'No. Once your payment is successfully completed, the registration fee is non-refundable under all circumstances (including withdrawal, late arrival, or inability to attend). If you are unable to attend after paying, you will receive digital access to the course materials only, but there will be no refund and no certificate, since attendance is mandatory for certification.'
    },
    
    // Equipment
    {
      category: 'Equipment',
      q: 'Do I need to bring my own drone or tools?',
      a: 'No. All components, training kits, tools, and safety equipment required for the workshop will be provided at IISc. You do not need to bring your own drone or hardwarejust bring yourself, a notebook, a laptop and enthusiasm to learn.'
    },
    {
      category: 'Equipment',
      q: 'Can I keep the drone I build?',
      a: 'The multirotors and fixed-wing aircraft used in the workshop are training kits belonging to the IISc UAV Laboratory. You will build, configure, and fly these platforms extensively during the workshop, but they remain assets of IISc after the event and are not take-home kits.'
    },
    
    // Workshop
    {
      category: 'Workshop',
      q: 'What will I learn during the five days?',
      a: 'Across five days, you\'ll move from fundamentals to live flight operations.\n  Theory sessions cover: Introduction to Aircraft & UAVs, Aerodynamics & Fixed-Wing Design, Avionics, GuidanceNavigationControl, Structures & Analysis, Propulsion, Ground Control, Drone Programming & ROS, and AI & OpenCV on Drones.\n  Three Practical sessions focus on hands-on multirotor and fixed-wing build and testing.\n  The final day is dedicated to supervised Flight Operations & Demonstration. The full tentative, day-wise schedule is available on the Program page.'
    },
    {
      category: 'Workshop',
      q: 'What is the level of the program?',
      a: 'Do Drones 2025 is designed for beginner to intermediate participants. You don\'t need prior experience with drones or programming, but familiarity with basic science and mathematics will help. The content also goes deep enough to benefit early-stage researchers and working professionals who want a strong practical foundation.'
    },
    
    // Certification
    {
      category: 'Certification',
      q: 'Will I receive a certificate?',
      a: 'Upon successful completion of the workshop and meeting the attendance requirements, you\'ll receive a prestigious certificate co-endorsed by IISc UAV Lab, HAL Skill Development Centre, and MeitY. This certificate validates your skills in drone technology and can strengthen your academic or professional profile.'
    },
    {
      category: 'Certification',
      q: 'Is there a minimum attendance requirement for the certificate?',
      a: 'Yes. A minimum of 75% attendance across all sessions (theory, practicals, and flight operations) is mandatory to qualify for the certificate. Attendance will be recorded for each session. Participants who do not meet this requirement will not receive the certificate, even if the fee has been paid in full.'
    },
    
    // Logistics
    {
      category: 'Logistics',
      q: 'Is accommodation provided?',
      a: 'Accommodation is not provided as part of the workshop fee. Participants are requested to arrange their own stay in Bengaluru. After your registration is confirmed, we can share recommendations for nearby hotels, PGs and hostels in and around the area. Accommodation will not be provided inside IISc'
    },
    {
      category: 'Logistics',
      q: 'Are meals provided during the workshop?',
      a: 'Yes. Lunch and refreshments are included on all five days of the workshop for registered participants.'
    },
    
    // Schedule
    {
      category: 'Schedule',
      q: 'What are the workshop timings?',
      a: 'The workshop runs from 9:00 AM to 5:30 PM each day, from 0913 December 2025. Please arrive by 8:30 AM on Day 1 for verification of registration and onboarding. The detailed tentative day-wise schedule is available on the Program page.'
    },
    
    // Safety
    {
      category: 'Safety',
      q: 'Is it safe for beginners?',
      a: 'Absolutely. Safety is our top priority. We have comprehensive safety protocols, trained instructors, and designated flying zones. Initial flights are conducted in controlled environments before moving outdoors.'
    },
    
    // Support
    {
      category: 'Support',
      q: 'What if I need help after the workshop?',
      a: 'You\'ll get access to our post-workshop support group, including our WhatsApp channel, where you can ask questions, share projects, and connect with fellow participants and instructors. We\'re committed to your continued learning journey.'
    }
  ]

  const categories = Array.from(new Set(faqs.map(faq => faq.category)))

  return (
    <div className="relative min-h-screen" style={{ background: 'var(--brand-black)' }}>
      {/* <Header /> - HIDDEN: Using TubelightNavBar instead */}
      <FlowBackdrop />
      
      {/* TASK 1: Hero Section */}
      <section className="relative z-10 pt-32 pb-20 px-4">
        <div className="container mx-auto max-w-6xl">
          <div className="text-center mb-8">
            <div className="flex justify-center mb-10">
              <div className="text-4xl md:text-5xl lg:text-6xl xl:text-7xl">
                <BrandTitle size="lg" variant="dark" />
              </div>
            </div>
            <h2 
              className="text-3xl md:text-5xl lg:text-6xl xl:text-7xl font-outfit font-light mb-6 leading-tight"
              style={{ color: 'var(--brand-white)' }}
            >
              questions? we've got <BesleyWord>answers</BesleyWord>.
            </h2>
            <p 
              className="text-xl md:text-2xl font-manrope max-w-3xl mx-auto mb-8"
              style={{ color: 'rgba(255,255,255,0.7)' }}
            >
              Everything you need to know about Do Drones 2025
            </p>
          </div>
        </div>
      </section>

      {/* TASK 2 & 3: FAQs Section */}
      <section 
        ref={addToRefs}
        className="reveal-section relative z-10 py-20 px-4"
      >
        <div className="container mx-auto max-w-4xl">
          {categories.map((category, catIdx) => (
            <div key={category} className="mb-12">
              <div 
                className="inline-block px-6 py-2 rounded-full mb-6"
                style={{
                  background: 'rgba(0,124,202,0.15)',
                  border: '1px solid rgba(0,124,202,0.3)'
                }}
              >
                <span 
                  className="text-sm font-manrope font-bold uppercase tracking-wider"
                  style={{ color: 'var(--brand-accent)' }}
                >
                  {category}
                </span>
              </div>
              
              <div className="space-y-4">
                {faqs
                  .filter(faq => faq.category === category)
                  .map((faq, idx) => {
                    const globalIdx = faqs.indexOf(faq)
                    const isOpen = openIndex === globalIdx
                    
                    return (
                      <div
                        key={idx}
                        className="stagger-item rounded-2xl overflow-hidden transition-all"
                        style={{
                          background: isOpen 
                            ? 'rgba(0,124,202,0.08)' 
                            : 'rgba(255,255,255,0.03)',
                          backdropFilter: 'blur(10px)',
                          border: isOpen 
                            ? '2px solid rgba(0,124,202,0.4)' 
                            : '1px solid rgba(255,255,255,0.1)',
                          animationDelay: `${idx * 0.05}s`
                        }}
                      >
                        <button
                          onClick={() => setOpenIndex(isOpen ? null : globalIdx)}
                          className="w-full p-6 text-left flex items-center justify-between gap-4 hover:bg-white/5 transition-colors"
                        >
                          <h3 
                            className="text-lg md:text-xl font-manrope font-bold"
                            style={{ color: 'var(--brand-white)' }}
                          >
                            {faq.q}
                          </h3>
                          <div
                            className="shrink-0 w-8 h-8 rounded-full flex items-center justify-center transition-transform"
                            style={{
                              background: 'rgba(0,124,202,0.2)',
                              transform: isOpen ? 'rotate(180deg)' : 'rotate(0deg)'
                            }}
                          >
                            <svg 
                              className="w-5 h-5" 
                              style={{ color: 'var(--brand-accent)' }} 
                              fill="none" 
                              stroke="currentColor" 
                              viewBox="0 0 24 24"
                            >
                              <path 
                                strokeLinecap="round" 
                                strokeLinejoin="round" 
                                strokeWidth={2} 
                                d="M19 9l-7 7-7-7" 
                              />
                            </svg>
                          </div>
                        </button>
                        
                        <div
                          className="overflow-hidden transition-all duration-300"
                          style={{
                            maxHeight: isOpen ? '800px' : '0px',
                            opacity: isOpen ? 1 : 0
                          }}
                        >
                          <div 
                            className="px-6 pb-6 pt-2"
                            style={{
                              borderTop: isOpen ? '1px solid rgba(255,255,255,0.1)' : 'none'
                            }}
                          >
                            <p 
                              className="text-base md:text-lg font-manrope leading-relaxed whitespace-pre-line"
                              style={{ color: 'rgba(255,255,255,0.8)' }}
                            >
                              {faq.a}
                            </p>
                          </div>
                        </div>
                      </div>
                    )
                  })}
              </div>
            </div>
          ))}
        </div>
      </section>

      {/* TASK 4: Still Have Questions - WhatsApp with InteractiveHoverButton */}
      <section 
        ref={addToRefs}
        className="reveal-section relative z-10 py-20 px-4"
      >
        <div className="container mx-auto max-w-4xl">
          <div 
            className="p-10 md:p-14 rounded-3xl text-center"
            style={{
              background: 'rgba(0,124,202,0.08)',
              backdropFilter: 'blur(20px)',
              border: '2px solid rgba(0,124,202,0.3)'
            }}
          >
            <div className="mb-8">
              <div 
                className="w-20 h-20 rounded-full flex items-center justify-center mx-auto mb-6"
                style={{ background: 'rgba(0,124,202,0.2)' }}
              >
                <svg 
                  className="w-10 h-10" 
                  style={{ color: 'var(--brand-accent)' }} 
                  fill="none" 
                  stroke="currentColor" 
                  viewBox="0 0 24 24"
                >
                  <path 
                    strokeLinecap="round" 
                    strokeLinejoin="round" 
                    strokeWidth={1.5} 
                    d="M8 12h.01M12 12h.01M16 12h.01M21 12c0 4.418-4.03 8-9 8a9.863 9.863 0 01-4.255-.949L3 20l1.395-3.72C3.512 15.042 3 13.574 3 12c0-4.418 4.03-8 9-8s9 3.582 9 8z" 
                  />
                </svg>
              </div>
              
              <h3 
                className="text-3xl md:text-4xl font-outfit font-light mb-4"
                style={{ color: 'var(--brand-white)' }}
              >
                Still have <BesleyWord>questions</BesleyWord>?
              </h3>
              <p 
                className="text-lg md:text-xl font-manrope mb-8"
                style={{ color: 'rgba(255,255,255,0.7)' }}
              >
                Join our WhatsApp channel to connect with the community,<br className="hidden md:block" />
                get real-time updates, and ask the team directly
              </p>
            </div>
            
            <div className="flex flex-col sm:flex-row gap-4 justify-center">
              <InteractiveHoverButton
                text="Join WhatsApp Channel"
                variant="whatsapp"
                onClick={handleWhatsAppClick}
                icon={
                  <svg 
                    className="w-6 h-6" 
                    fill="currentColor" 
                    viewBox="0 0 24 24"
                  >
                    <path d="M17.472 14.382c-.297-.149-1.758-.867-2.03-.967-.273-.099-.471-.148-.67.15-.197.297-.767.966-.94 1.164-.173.199-.347.223-.644.075-.297-.15-1.255-.463-2.39-1.475-.883-.788-1.48-1.761-1.653-2.059-.173-.297-.018-.458.13-.606.134-.133.298-.347.446-.52.149-.174.198-.298.298-.497.099-.198.05-.371-.025-.52-.075-.149-.669-1.612-.916-2.207-.242-.579-.487-.5-.669-.51-.173-.008-.371-.01-.57-.01-.198 0-.52.074-.792.372-.272.297-1.04 1.016-1.04 2.479 0 1.462 1.065 2.875 1.213 3.074.149.198 2.096 3.2 5.077 4.487.709.306 1.262.489 1.694.625.712.227 1.36.195 1.871.118.571-.085 1.758-.719 2.006-1.413.248-.694.248-1.289.173-1.413-.074-.124-.272-.198-.57-.347m-5.421 7.403h-.004a9.87 9.87 0 01-5.031-1.378l-.361-.214-3.741.982.998-3.648-.235-.374a9.86 9.86 0 01-1.51-5.26c.001-5.45 4.436-9.884 9.888-9.884 2.64 0 5.122 1.03 6.988 2.898a9.825 9.825 0 012.893 6.994c-.003 5.45-4.437 9.884-9.885 9.884m8.413-18.297A11.815 11.815 0 0012.05 0C5.495 0 .16 5.335.157 11.892c0 2.096.547 4.142 1.588 5.945L.057 24l6.305-1.654a11.882 11.882 0 005.683 1.448h.005c6.554 0 11.89-5.335 11.893-11.893a11.821 11.821 0 00-3.48-8.413Z"/>
                  </svg>
                }
              />
            </div>
          </div>
        </div>
      </section>

      {/* TASK 5: Bottom CTA Section */}
      <section 
        ref={addToRefs}
        className="reveal-section relative z-10 py-20 px-4 mb-20"
      >
        <div 
          className="container mx-auto max-w-4xl text-center p-12 rounded-3xl"
          style={{
            background: 'rgba(0,124,202,0.1)',
            backdropFilter: 'blur(20px)',
            border: '2px solid rgba(0,124,202,0.3)'
          }}
        >
          <h3 
            className="text-3xl md:text-5xl font-outfit font-light mb-6"
            style={{ color: 'var(--brand-white)' }}
          >
            ready to <BesleyWord>get started</BesleyWord>?
          </h3>
          <p 
            className="text-xl font-manrope mb-8"
            style={{ color: 'rgba(255,255,255,0.7)' }}
          >
            Limited seats. 0913 December 2025. Don't miss out.
          </p>
          <div className="flex justify-center">
            <InteractiveHoverButton
              text="Register Now"
              variant="primary"
              onClick={handleRegisterClick}
              className={DISABLE_LOGIN_PAGE?" disabled-link":""}
            />
          </div>
        </div>
      </section>

      <Footer />

      <style jsx>{`
        .reveal-section {
          opacity: 0;
          transform: translateY(50px);
          transition: opacity 1s ease, transform 1s ease;
        }

        .reveal-section.visible {
          opacity: 1;
          transform: translateY(0);
        }

        .stagger-item {
          opacity: 0;
          transform: translateY(30px);
          animation: none;
        }

        .reveal-section.visible .stagger-item {
          animation: fadeInUp 0.8s ease forwards;
        }

        @keyframes fadeInUp {
          to {
            opacity: 1;
            transform: translateY(0);
          }
        }

        @media (prefers-reduced-motion: reduce) {
          .reveal-section,
          .stagger-item {
            transition: none;
            animation: none;
            opacity: 1;
            transform: none;
          }
        }
      `}</style>
    </div>
  )
}
