'use client'

import { useEffect, useRef } from 'react'
import { useNavigate } from 'react-router-dom';
import Header from '../components/Header'
import BrandTitle from '../components/BrandTitle'
import FlowBackdrop from '../components/FlowBackdrop'
import { InteractiveHoverButton } from '../components/ui/interactive-hover-button'
import Footer from '../components/Footer'
import BesleyWord from '../components/BesleyWord'
import { siteConfig } from '../lib/siteConfig'

export default function ProgramPageNew() {
  const navigate = useNavigate();
  const sectionsRef = useRef<(HTMLElement | null)[]>([])

  useEffect(() => {
    const observerOptions = {
      threshold: 0.1,
      rootMargin: '0px 0px -10% 0px'
    }

    const observer = new IntersectionObserver((entries) => {
      entries.forEach((entry) => {
        if (entry.isIntersecting) {
          entry.target.classList.add('visible')
        }
      })
    }, observerOptions)

    sectionsRef.current.forEach((section) => {
      if (section) observer.observe(section)
    })

    return () => observer.disconnect()
  }, [])

  const addToRefs = (el: HTMLElement | null) => {
    if (el && !sectionsRef.current.includes(el)) {
      sectionsRef.current.push(el)
    }
  }

  const handleRegisterClick = () => {
    navigate('/login')
  }

  const handleMapsClick = () => {
    window.open('https://www.google.com/maps/place/Department+of+Aerospace+Engineering,+Indian+Institute+of+Science/@13.0217062,77.5649612,17z', '_blank', 'noopener,noreferrer')
  }

  return (
    <div className="relative min-h-screen" style={{ background: 'var(--brand-black)' }}>
      <Header />
      <FlowBackdrop />
      
      {/* PHASE 1: Hero Section - 5-day workshop */}
      <section className="relative z-10 pt-32 pb-20 px-4">
        <div className="container mx-auto max-w-6xl">
          <div className="text-center mb-8">
            <p 
              className="text-sm uppercase tracking-widest mb-6 font-manrope font-medium"
              style={{ color: 'var(--blue-100)', opacity: 0.8 }}
            >
              do drones 2025.
            </p>
            <h1
              className="text-4xl md:text-6xl font-outfit font-light mb-6 leading-tight"
              style={{ color: 'var(--brand-white)' }}
            >
              Your <BesleyWord>journey</BesleyWord> to mastering aerial robotics
            </h1>
            <p 
              className="text-xl md:text-2xl font-manrope max-w-3xl mx-auto mb-8"
              style={{ color: 'rgba(255,255,255,0.7)' }}
            >
              A comprehensive five-day workshop that takes you from first principles to confident multirotor and fixed-wing flight, taught by the IISc UAV Lab team at India's premier aerospace research institute.
            </p>
          </div>
        </div>
      </section>

      {/* PHASE 2: What's Included Section */}
      <section 
        ref={addToRefs}
        className="reveal-section relative z-10 py-20 px-4"
      >
        <div className="container mx-auto max-w-6xl">
          <h2
            className="text-3xl md:text-5xl font-outfit font-light mb-12 text-center"
            style={{ color: 'var(--brand-white)' }}
          >
            what's <BesleyWord>included</BesleyWord>?
          </h2>
          
          <div className="grid md:grid-cols-3 gap-8">
            {[
              {
                title: 'Hands-on Training Kits & Hardware',
                items: [
                  'Use complete multirotor and fixed-wing training kits during guided sessions.',
                  'Access flight controllers, sensors, radios, and ground-control systems used in real UAV projects.',
                  'Work with calibrated tools, test rigs, and lab infrastructure from the IISc UAV Laboratory.',
                  'Practice safe assembly, calibration, and troubleshooting under mentor supervision.'
                ]
              },
              {
                title: 'Expert-Led Teaching & Mentorship',
                items: [
                  'Learn directly from IISc faculty, researchers, and experienced UAV practitioners.',
                  'Experience a structured blend of theory, demonstrations, and hands-on labs across five days.',
                  'Get step-by-step guidance on avionics setup, mission planning, and flight testing.',
                  'Interact in small groups for Q&A, debugging help, and personalised feedback.'
                ]
              },
              {
                title: 'Labs, Flights & Learning Resources',
                items: [
                  'Perform indoor tests, outdoor demonstrations, and simulator-based training as per the schedule.',
                  'Observe full mission workflows from pre-flight checks to post-flight debriefs.',
                  'Receive curated reference material and workshop notes for continued self-study.',
                  'Earn a co-endorsed certificate of completion from IISc UAV Lab, HAL SDC, and MeitY.'
                ]
              }
            ].map((item, idx) => (
              <div
                key={idx}
                className="stagger-item"
                style={{
                  background: 'rgba(255,255,255,0.05)',
                  backdropFilter: 'blur(10px)',
                  border: '1px solid rgba(255,255,255,0.1)',
                  borderRadius: '16px',
                  padding: '32px',
                  animationDelay: `${idx * 0.1}s`
                }}
              >
                <h3
                  className="text-2xl font-manrope font-bold mb-4"
                  style={{ color: 'var(--brand-accent)' }}
                >
                  {item.title}
                </h3>
                <ul className="space-y-3">
                  {item.items.map((subitem, subidx) => (
                    <li
                      key={subidx}
                      className="flex items-start gap-3"
                      style={{ color: 'rgba(255,255,255,0.8)' }}
                    >
                      <span style={{ color: 'var(--brand-accent)' }}></span>
                      <span className="font-manrope text-sm">{subitem}</span>
                    </li>
                  ))}
                </ul>
              </div>
            ))}
          </div>
          
          {/* Kit Note */}
          <p 
            className="text-sm text-center mt-8"
            style={{ color: 'rgba(255,255,255,0.4)' }}
          >
            Note: Training kits are issued for on-campus use during the workshop and remain the property of IISc after the event.
          </p>
        </div>
      </section>

      {/* PHASE 5: Key Takeaways */}
      <section 
        ref={addToRefs}
        className="reveal-section relative z-10 py-20 px-4"
      >
        <div className="container mx-auto max-w-5xl">
          <h2
            className="text-3xl md:text-5xl font-outfit font-light mb-12 text-center"
            style={{ color: 'var(--brand-white)' }}
          >
            key <BesleyWord>takeaways</BesleyWord>.
          </h2>
          
          <div className="grid md:grid-cols-2 gap-6">
            {[
              'Build and work with complete multirotor and fixed-wing UAV training platforms from structure to avionics.',
              'Develop a solid grasp of aerodynamics, propulsion, avionics, and guidancenavigationcontrol for small UAVs.',
              'Gain practical experience in assembly, calibration, and pre-flight checks under expert supervision.',
              'Learn to plan, configure, and monitor autonomous missions using professional ground-control software.',
              'Understand key DGCA regulations, safety protocols, and responsible UAS operations in the Indian context.',
              'Observe live indoor and outdoor flight demonstrations that connect classroom concepts to real missions.',
              'Interact with IISc researchers, industry experts, and fellow participants to explore projects and career paths.',
              'Receive a co-endorsed certificate of completion recognising your participation in Do Drones 2025.'
            ].map((takeaway, idx) => (
              <div
                key={idx}
                className="flex items-start gap-4 p-6 rounded-xl stagger-item"
                style={{
                  background: 'rgba(255,255,255,0.03)',
                  border: '1px solid rgba(255,255,255,0.1)',
                  animationDelay: `${idx * 0.05}s`
                }}
              >
                <div
                  className="w-8 h-8 rounded-full flex items-center justify-center shrink-0 mt-1"
                  style={{ 
                    background: 'var(--brand-accent)',
                    color: 'var(--brand-white)'
                  }}
                >
                  <span className="text-sm font-bold"></span>
                </div>
                <span 
                  className="text-lg font-manrope"
                  style={{ color: 'rgba(255,255,255,0.9)' }}
                >
                  {takeaway}
                </span>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* PHASE 3: Workshop Schedule - 5 Days, Tentative */}
      <section 
        ref={addToRefs}
        className="reveal-section relative z-10 py-20 px-4"
      >
        <div className="container mx-auto max-w-5xl">
          <h2
            className="text-3xl md:text-5xl font-outfit font-light mb-6 text-center"
            style={{ color: 'var(--brand-white)' }}
          >
            workshop <BesleyWord>details</BesleyWord>.
          </h2>
          
          <p 
            className="text-center text-sm mb-12 font-manrope"
            style={{ color: 'rgba(255,255,255,0.6)' }}
          >
            This is a tentative schedule and may be updated closer to the workshop date.
          </p>

          {/* Schedule will continue in next message due to token limits */}
          
        </div>
      </section>

      <Footer />

      <style jsx>{`
        .reveal-section {
          opacity: 0;
          transform: translateY(50px);
          transition: opacity 1s ease, transform 1s ease;
        }

        .reveal-section.visible {
          opacity: 1;
          transform: translateY(0);
        }

        .stagger-item {
          opacity: 0;
          transform: translateY(30px);
          animation: none;
        }

        .reveal-section.visible .stagger-item {
          animation: fadeInUp 0.8s ease forwards;
        }

        @keyframes fadeInUp {
          to {
            opacity: 1;
            transform: translateY(0);
          }
        }

        @media (prefers-reduced-motion: reduce) {
          .reveal-section,
          .stagger-item {
            transition: none;
            animation: none;
            opacity: 1;
            transform: none;
          }
        }
      `}</style>
    </div>
  )
}
