/**
 * InteractiveHoverButton Component
 * 
 * Brand-consistent animated button with hover effects:
 * - Transparent border by default
 * - Fills with brand color on hover
 * - Text slides out while arrow slides in
 * - Expanding colored blob background animation
 * 
 * USAGE:
 * <InteractiveHoverButton
 *   text="Button Text"
 *   variant="primary" | "secondary" | "accent"
 *   onClick={handleClick}
 * />
 * 
 * VARIANTS:
 * - primary: Blue border  Blue fill (hero CTA)
 * - secondary: Light border  Light fill (view program, login)
 * - accent: Orange border  Orange fill (Join Channel)
 */

import React from "react";
import { ArrowRight } from "lucide-react";
import { cn } from "../../lib/utils";

type ButtonVariant = "primary" | "secondary" | "accent" | "whatsapp";

interface InteractiveHoverButtonProps
  extends React.ButtonHTMLAttributes<HTMLButtonElement> {
  text?: string;
  variant?: ButtonVariant;
  href?: string;
  asLink?: boolean;
  icon?: React.ReactNode;
}

const InteractiveHoverButton = React.forwardRef<
  HTMLButtonElement,
  InteractiveHoverButtonProps
>(({ text = "Button", variant = "primary", className, href, asLink, icon, ...props }, ref) => {
  // Debug log
  if (text === "Join Channel") {
    console.log("Join Channel button variant:", variant);
  }
  
  // Variant-specific styling
  const variantClasses = {
    primary: cn(
      // Default: transparent with blue border
      "border-[#2563eb] text-[#3b82f6]",
      // Hover: blue fill with white text
      "hover:bg-[#2563eb] hover:text-white hover:border-[#2563eb]",
      // Blob color for animation
      "[&_.blob]:bg-[#2563eb]"
    ),
    secondary: cn(
      // Default: transparent with light border
      "border-slate-600/60 text-slate-100",
      // Hover: light fill with dark text
      "hover:bg-slate-100/10 hover:text-white hover:border-slate-500",
      // Blob color for animation
      "[&_.blob]:bg-slate-100/10"
    ),
    accent: cn(
      // Default: transparent with orange border
      "border-[#f97316] text-[#fb923c]",
      // Hover: orange fill with white text
      "hover:bg-[#f97316] hover:text-white hover:border-[#f97316]",
      // Blob color for animation
      "[&_.blob]:bg-[#f97316]"
    ),
    whatsapp: cn(
      // Default: transparent with green border
      "border-[#25D366] text-[#25D366]",
      // Hover: green fill with white text
      "hover:bg-[#25D366] hover:text-white hover:border-[#25D366]",
      // Blob color for animation
      "[&_.blob]:bg-[#25D366]"
    ),
  };

  const baseClasses = cn(
    // Core structure
    "group relative inline-flex items-center justify-center cursor-pointer overflow-hidden",
    // Pill shape with EXTRA generous sizing
    "rounded-full border-2 min-h-[48px] md:min-h-[52px]",
    // Increased padding for MORE breathing room
    "px-8 sm:px-10 md:px-12 lg:px-14 xl:px-16",
    // Vertical padding for better proportion
    "py-3 md:py-3.5",
    // Typography - slightly larger for prominence
    "font-manrope font-semibold text-sm md:text-base lg:text-lg tracking-tight",
    // Default transparent background
    "bg-transparent",
    // Smooth transitions
    "transition-all duration-300 ease-in-out",
    // Hover shadow for depth
    "hover:shadow-lg",
    // Variant-specific colors
    variantClasses[variant],
    // Custom classes from props
    className
  );

  const content = (
    <>
      {/* Default text with optional icon that slides out on hover */}
      <span className="relative z-20 inline-flex items-center justify-center gap-3 whitespace-nowrap transition-all duration-300 group-hover:translate-x-12 group-hover:opacity-0">
        {icon && <span className="shrink-0">{icon}</span>}
        {text}
      </span>
      
      {/* Hover text with icon and arrow that slides in */}
      <div className="absolute inset-0 z-20 flex items-center justify-center gap-2 opacity-0 transition-all duration-300 group-hover:opacity-100 group-hover:translate-x-0 translate-x-12">
        {icon && <span className="shrink-0">{icon}</span>}
        <span className="whitespace-nowrap">{text}</span>
        <ArrowRight className="h-4 w-4 md:h-5 md:w-5" />
      </div>
      
      {/* Expanding blob animation - Centered with better spacing */}
      <div 
        className="blob absolute left-1/2 top-1/2 -translate-x-1/2 -translate-y-1/2 h-3 w-3 scale-[1] rounded-full opacity-60 transition-all duration-300 ease-out group-hover:left-1/2 group-hover:top-1/2 group-hover:h-full group-hover:w-full group-hover:scale-[2.5] group-hover:opacity-0"
        style={{ zIndex: 1 }}
      />
    </>
  );

  // Render as anchor if href is provided
  if (href || asLink) {
    return (
      <a
        href={href}
        className={baseClasses}
        ref={ref as any}
        {...(props as any)}
      >
        {content}
      </a>
    );
  }

  return (
    <button ref={ref} className={baseClasses} {...props}>
      {content}
    </button>
  );
});

InteractiveHoverButton.displayName = "InteractiveHoverButton";

export { InteractiveHoverButton };
export type { ButtonVariant };
