/**
 * Header component with mobile hamburger menu
 * 
 * Desktop (lg+): Shows inline navigation with all links
 * Mobile (<lg): Shows hamburger button that toggles dropdown panel with all nav items
 * 
 * All navigation is frontend-only, config-driven from siteConfig.ts
 */

"use client";

import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Link } from 'react-router-dom';
import BrandTitle from '../components/BrandTitle';
import { InteractiveHoverButton } from '../components/ui/interactive-hover-button';
import { siteConfig } from '../lib/siteConfig';
import { cn } from '../lib/utils';
import { DISABLE_ABOUT_PAGE,DISABLE_LOGIN_PAGE } from '../flags';

export default function Header() {
  const navigate=useNavigate();
  const whatsappUrl = siteConfig.urls.whatsappChannel;
  const [isOpen, setIsOpen] = useState(false);
  
  const handleLoginClick = () => {
    navigate('/login');
  };
  
  const handleJoinChannelClick = () => {
    window.open(whatsappUrl, '_blank', 'noopener,noreferrer');
  };
  
  return (
    <header 
      className="shadow-md relative"
      style={{ background: 'var(--brand-black)', color: 'var(--brand-white)' }}
    >
      <div className="container mx-auto px-4 py-4">
        <div className="flex items-center justify-between">
          <Link 
            to="/" 
            className="hover:opacity-90 transition shrink-0"
          >
            <BrandTitle size="sm" variant="dark" />
          </Link>
          
          {/* Desktop Navigation (lg and up) */}
          <nav className="hidden lg:flex items-center gap-4 xl:gap-6">
            <Link 
              to="/program" 
              className="hover:opacity-90 transition font-manrope text-sm xl:text-base px-2"
              style={{ color: 'var(--brand-white)' }}
            >
              Program
            </Link>
            <Link 
              to="/about" 
              className={"hover:opacity-90 transition font-manrope text-sm xl:text-base px-2"+(DISABLE_ABOUT_PAGE?" disabled-link":"")}
              style={{ color: 'var(--brand-white)' }}
            >
              About
            </Link>
            <Link 
              to="/faq" 
              className="hover:opacity-90 transition font-manrope text-sm xl:text-base px-2"
              style={{ color: 'var(--brand-white)' }}
            >
              FAQ
            </Link>
            
            {/* Interactive Login Button (Secondary) */}
            <InteractiveHoverButton
              text="Login"
              variant="secondary"
              onClick={handleLoginClick}
              className={"text-xs sm:text-sm"+(DISABLE_LOGIN_PAGE?" disabled-link":"")}
            />
            
            {/* Interactive Join WhatsApp Channel Button (Green) */}
            <InteractiveHoverButton
              text="Join Channel"
              variant="whatsapp"
              onClick={handleJoinChannelClick}
              className="text-xs sm:text-sm"
              icon={
                <svg 
                  className="w-5 h-5" 
                  fill="currentColor" 
                  viewBox="0 0 24 24"
                >
                  <path d="M17.472 14.382c-.297-.149-1.758-.867-2.03-.967-.273-.099-.471-.148-.67.15-.197.297-.767.966-.94 1.164-.173.199-.347.223-.644.075-.297-.15-1.255-.463-2.39-1.475-.883-.788-1.48-1.761-1.653-2.059-.173-.297-.018-.458.13-.606.134-.133.298-.347.446-.52.149-.174.198-.298.298-.497.099-.198.05-.371-.025-.52-.075-.149-.669-1.612-.916-2.207-.242-.579-.487-.5-.669-.51-.173-.008-.371-.01-.57-.01-.198 0-.52.074-.792.372-.272.297-1.04 1.016-1.04 2.479 0 1.462 1.065 2.875 1.213 3.074.149.198 2.096 3.2 5.077 4.487.709.306 1.262.489 1.694.625.712.227 1.36.195 1.871.118.571-.085 1.758-.719 2.006-1.413.248-.694.248-1.289.173-1.413-.074-.124-.272-.198-.57-.347m-5.421 7.403h-.004a9.87 9.87 0 01-5.031-1.378l-.361-.214-3.741.982.998-3.648-.235-.374a9.86 9.86 0 01-1.51-5.26c.001-5.45 4.436-9.884 9.888-9.884 2.64 0 5.122 1.03 6.988 2.898a9.825 9.825 0 012.893 6.994c-.003 5.45-4.437 9.884-9.885 9.884m8.413-18.297A11.815 11.815 0 0012.05 0C5.495 0 .16 5.335.157 11.892c0 2.096.547 4.142 1.588 5.945L.057 24l6.305-1.654a11.882 11.882 0 005.683 1.448h.005c6.554 0 11.89-5.335 11.893-11.893a11.821 11.821 0 00-3.48-8.413Z"/>
                </svg>
              }
            />
          </nav>
          
          {/* Mobile: Login + Hamburger (< lg) */}
          <div className="flex lg:hidden items-center gap-2">
            <InteractiveHoverButton
              text="Login"
              variant="secondary"
              onClick={handleLoginClick}
              className={"text-xs px-4"+(DISABLE_LOGIN_PAGE?" disabled-link":"")}
            />
            
            {/* Hamburger Menu Button */}
            <button
              type="button"
              onClick={() => setIsOpen(!isOpen)}
              aria-label={isOpen ? "Close navigation menu" : "Open navigation menu"}
              aria-expanded={isOpen}
              aria-controls="mobile-nav-header"
              className="relative flex h-10 w-10 flex-col items-center justify-center gap-[6px] rounded-full border border-white/10 bg-transparent outline-none transition-all duration-200 hover:border-[var(--brand-primary)]/70 hover:bg-white/5 focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-[var(--brand-primary)]"
            >
              <span
                className={cn(
                  "burger-line",
                  isOpen && "burger-line-top-open"
                )}
              />
              <span
                className={cn(
                  "burger-line",
                  isOpen && "burger-line-middle-open"
                )}
              />
              <span
                className={cn(
                  "burger-line",
                  isOpen && "burger-line-bottom-open"
                )}
              />
            </button>
          </div>
        </div>
        
        {/* Mobile Menu Dropdown */}
        {isOpen && (
          <div 
            id="mobile-nav-header"
            className="lg:hidden mt-2 space-y-2 rounded-2xl border px-3 py-4"
            style={{
              background: 'rgba(0,0,0,0.95)',
              borderColor: 'rgba(255,255,255,0.05)',
              backdropFilter: 'blur(16px)'
            }}
          >
            <Link 
              to="/program" 
              className="block hover:opacity-90 transition font-manrope text-base py-3 min-h-[44px]"
              style={{ color: 'var(--brand-white)' }}
              onClick={() => setIsOpen(false)}
            >
              Program
            </Link>
            <Link 
              to="/about" 
              className={"block hover:opacity-90 transition font-manrope text-base py-3 min-h-[44px]"+(DISABLE_ABOUT_PAGE?" disabled-link":"")}
              style={{ color: 'var(--brand-white)' }}
              onClick={() => setIsOpen(false)}
            >
              About
            </Link>
            <Link 
              to="/faq" 
              className="block hover:opacity-90 transition font-manrope text-base py-3 min-h-[44px]"
              style={{ color: 'var(--brand-white)' }}
              onClick={() => setIsOpen(false)}
            >
              FAQ
            </Link>
            
            {/* Divider */}
            <div 
              className="h-px my-2"
              style={{ background: 'rgba(255,255,255,0.1)' }}
            />
            
            {/* Mobile Join WhatsApp Channel Button (Green) */}
            <InteractiveHoverButton
              text="Join WhatsApp Channel"
              variant="whatsapp"
              onClick={() => {
                handleJoinChannelClick();
                setIsOpen(false);
              }}
              className="w-full mt-2"
              icon={
                <svg 
                  className="w-5 h-5" 
                  fill="currentColor" 
                  viewBox="0 0 24 24"
                >
                  <path d="M17.472 14.382c-.297-.149-1.758-.867-2.03-.967-.273-.099-.471-.148-.67.15-.197.297-.767.966-.94 1.164-.173.199-.347.223-.644.075-.297-.15-1.255-.463-2.39-1.475-.883-.788-1.48-1.761-1.653-2.059-.173-.297-.018-.458.13-.606.134-.133.298-.347.446-.52.149-.174.198-.298.298-.497.099-.198.05-.371-.025-.52-.075-.149-.669-1.612-.916-2.207-.242-.579-.487-.5-.669-.51-.173-.008-.371-.01-.57-.01-.198 0-.52.074-.792.372-.272.297-1.04 1.016-1.04 2.479 0 1.462 1.065 2.875 1.213 3.074.149.198 2.096 3.2 5.077 4.487.709.306 1.262.489 1.694.625.712.227 1.36.195 1.871.118.571-.085 1.758-.719 2.006-1.413.248-.694.248-1.289.173-1.413-.074-.124-.272-.198-.57-.347m-5.421 7.403h-.004a9.87 9.87 0 01-5.031-1.378l-.361-.214-3.741.982.998-3.648-.235-.374a9.86 9.86 0 01-1.51-5.26c.001-5.45 4.436-9.884 9.888-9.884 2.64 0 5.122 1.03 6.988 2.898a9.825 9.825 0 012.893 6.994c-.003 5.45-4.437 9.884-9.885 9.884m8.413-18.297A11.815 11.815 0 0012.05 0C5.495 0 .16 5.335.157 11.892c0 2.096.547 4.142 1.588 5.945L.057 24l6.305-1.654a11.882 11.882 0 005.683 1.448h.005c6.554 0 11.89-5.335 11.893-11.893a11.821 11.821 0 00-3.48-8.413Z"/>
                </svg>
              }
            />
          </div>
        )}
      </div>
    </header>
  );
}
