import { Link } from 'react-router-dom'
import { siteConfig } from '../lib/siteConfig'
import { DISABLE_ABOUT_PAGE} from "../flags"
export default function Footer() {
  return (
    <footer 
      className="mt-16 md:mt-20 border-t"
      style={{ 
        background: 'var(--brand-black)', 
        color: 'var(--brand-white)',
        borderColor: 'rgba(255,255,255,0.1)'
      }}
    >
      <div className="container mx-auto px-4 py-10 md:py-14">
        {/* Grid layout: single column on mobile, 3 columns on desktop */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-8 md:gap-10">
          {/* Column 1: Event Info */}
          <div className="text-center md:text-left">
            <h3 className="font-manrope font-bold text-lg md:text-xl mb-3 md:mb-4">Do Drones 2025</h3>
            <p className="text-sm md:text-base" style={{ opacity: 0.7 }}>
              IISc UAV Lab  HAL SDC  MeitY
            </p>
            <p className="text-sm md:text-base mt-2" style={{ opacity: 0.7 }}>
              {siteConfig.eventDatesShort}
            </p>
            <a
              href={siteConfig.venue.googleMapsUrl}
              target="_blank"
              rel="noopener noreferrer"
              className="text-sm md:text-base mt-2 inline-block hover:opacity-90 transition"
              style={{ opacity: 0.7 }}
            >
              {siteConfig.venue.fullAddress}
            </a>
          </div>
          
          {/* Column 2: Quick Links */}
          <div className="text-center md:text-left">
            <h4 className="font-semibold mb-3 md:mb-4 text-base md:text-lg">Quick Links</h4>
            <ul className="space-y-2 text-sm md:text-base">
              <li><Link to="/program" className="hover:opacity-90 transition min-h-[44px] inline-block" style={{ opacity: 0.8 }}>Program</Link></li>
              <li><Link to="/about" className={"hover:opacity-90 transition min-h-[44px] inline-block"+(DISABLE_ABOUT_PAGE?" disabled-link":"")} style={{ opacity: 0.8 }}>About</Link></li>
              <li><Link to="/faq" className="hover:opacity-90 transition min-h-[44px] inline-block" style={{ opacity: 0.8 }}>FAQ</Link></li>
            </ul>
          </div>

          {/* Column 3: Connect with Us */}
          <div className="text-center md:text-left">
            <h4 className="font-semibold mb-3 md:mb-4 text-base md:text-lg">Connect with Us</h4>
            <ul className="space-y-2 text-sm md:text-base">
              <li>
                <a 
                  href="https://www.instagram.com/omkar_lab.iisc?igsh=MXVibXhoaXU5aHhvOA==" 
                  target="_blank" 
                  rel="noopener noreferrer"
                  className="hover:opacity-90 transition min-h-[44px] inline-block" 
                  style={{ opacity: 0.8 }}
                >
                  Instagram
                </a>
              </li>
              <li>
                <a 
                  href="mailto:contact@dodrones.in"
                  className="hover:opacity-90 transition min-h-[44px] inline-block" 
                  style={{ opacity: 0.8 }}
                >
                  contact@dodrones.in
                </a>
              </li>
              <li style={{ opacity: 0.8 }}>
                <div className="text-sm md:text-base">Tarun S - 7022070034</div>
              </li>
              <li style={{ opacity: 0.8 }}>
                <div className="text-sm md:text-base">Phelixa J - 8971528518</div>
              </li>
            </ul>
          </div>
        </div>
        
        <div className="mt-10 md:mt-12 pt-8 md:pt-10 text-center" style={{ borderTop: '1px solid rgba(255,255,255,0.1)' }}>
          <p style={{ opacity: 0.6 }}> 2025 Do Drones, IISc Bangalore. All rights reserved.</p>
        </div>
      </div>
    </footer>
  )
}
